<template>
  <div class="ewm" v-if="flag">
    <div class="ewm-dialog">
      <img src="http://resource.yatiku.com/resources/share/studentrecode.jpg" alt="">
      <p>您已经在中药群了<br>如需换西药群请直接联系老师</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'signIn',
  props: ['flag'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    signIn() {
      this.$emit('emitSignIn', false)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.ewm{
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.5);
  .ewm-dialog{
    width: 50%;
    padding: 30px 20px 50px 20px;
    background: #fff;
    border-radius: 6px;
    img{
      width: 100%;
    }
    p{
      margin-top: 20px;
      line-height: 24px;
      text-align: center;
      font-size: $font14;
    }
  }
}
</style>



