<template>
  <div class="announcement">
   <div class="notice">
	<span>标题：</span>
	<textarea v-model="titletxt"></textarea>	
   </div>
		
	<div class="choosecheck">
		<input type="checkbox" id="service" value="service" v-model.trim="checkedservice">
		<label for="service">服务号</label>
		<input type="checkbox" id="group" value="group" v-model.trim="checkedgroup">
		<label for="group">群系统</label>
	</div>
	<button class="sendButton" :disabled="isClick" @click="send">确认发送</button>
  </div>
</template>

<script>
	import Vue from "vue"
	import { Toast } from 'mint-ui';
export default {
  name: 'announcement',
  data () {
    return {
		titletxt:"",
		checkedservice: true,
		checkedservice_num: "",
		checkedgroup: true,
		checkedgroup_num: "",
		isClick: false
    }
  },
  methods: {
	// 确认发送
	send: function() {
		if( this.titletxt == "") {
			Toast({
				message: '请输入内容',
				position: 'middle',
				duration: 1000
			});
		}else {
			this.checkedgroup_num = this.checkedgroup ? 1 : 0;
			this.checkedservice_num = this.checkedservice ? 1 : 0;
			this.isClick = true;
			this._get(`${this.$store.state.url}/wx/powderSystem/addNoticeInfo`, {
				title: this.titletxt,
				url: '',
				type: 1,
				groupId: userObj.groupId,
				adminId: userObj.adminId,
				sendClient: this.checkedgroup_num,
				sendServer: this.checkedservice_num
			}, (res) => {
				if (res.status === 200) {
					this.isClick = false;
					Toast({
						message: '操作成功',
						position: 'middle',
						duration: 1000
					});
					let obj = {};
					if (res.body) {
						obj = {
							id: res.body,
							title: this.titletxt,
							url: '',
							dataType: 7
						};
					}
					this.$router.push({path: routerObj.home});
					this.$emit('announcementSend',obj);
					setTimeout(() => {
						this.clearData();
					},500);
				}
			});
		}

	},
	// 清空
	clearData() {
		this.titletxt = '';
	},
	_get(url, data, cb) {
		this.$http({
			url: url,
			method: 'GET',
			params: data
		}).then( res => {
			cb && cb(res);
		}, err => {
			console.log(err);
		});
	}
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/css/var.scss';
.notice {
	height: 120px;
	border: 1px solid $bordercolor;
	background-color: $whitecolor;
	padding: 20px;
	box-sizing: border-box;
	span{
		vertical-align: top;
	}
	textarea{
		width: 80%;
		height: 69px;
		vertical-align: top;
		border: none;
		resize: none;
		line-height: 20px;
	}
}
.sendButton {
	display: block;
	height: 50px;
	line-height: 50px;
	color: #fff;
	text-align: center;
	font-size: $font18;
	background-color: $bluecolor;
	width: 90%;
	margin: 0 auto;
	margin-top: 20px;
	border-radius: 3px;
}
.choosecheck {
	height: 40px;
	line-height: 40px;
	margin-left: 5%;
}
</style>



