<template>
	<div class="register">
		<div class="register_page" v-show="toggle">
			<div class="register_div">
				<i class="iconfont icon-nicheng"></i>
				<input type="text" placeholder="请输入昵称" v-model.trim="nickname" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-shouji"></i>
				<input type="tel" placeholder="请输入手机号" v-model.trim="phone" maxlength="11" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-mima"></i>
				<input type="password" placeholder="请输入密码" v-model.trim="password" />
			</div>
			<div class="verification ">
				<input type="tel" placeholder="请输入验证码" v-model.trim="verification_code"  maxlength="4" />
				<img :src="$store.state.url + '/tool/code'" class="verification_img" />
			</div>
			<p class="nextone" @click="clicknext">下一步</p>
		</div>
		<div class="phone_code_page register_page" v-show="toggle2">
			<div class="verification ">
				<input type="tel" placeholder="请输入手机号" v-model.trim="phone"  maxlength="11" />
				<p v-if="isActive" @click="resend" :class="{'resends':isActive,'blue':blueactive}">重新获取<i v-for="items in list">（{{ items.time }}）</i></p>
				<p v-else @click="resend" :class="{'resends':isActive,'blue':blueactive}">重新获取</p>

			</div>
			<div class="register_div">

				<input type="tel" placeholder="请输入手机验证码" v-model.trim="phone_verification"  maxlength="6" />
			</div>
			<p class="nextone" @click="clickregister">注册</p>

		</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import { Toast } from 'mint-ui';
	var times
	var times2
	export default {
		name: 'register',
		data() {
			return {
				nickname: "",
				phone: "",
				password: "",
				verification_code: "",
				phone_num: "",
				verification_result: '',
				guid: "",
				toggle: true,
				toggle2: false,
				list: [{}],
				phone_verification: "",
				isActive: true,
				blueactive: false
			}
		},
		methods: {
			clicknext() {
				this.guid=localStorage.getItem("txq_unionid");

				if (!this.nickname.length) {
					Toast({
						message: '昵称不能为空',
						position: 'middle',
						duration: 1000
					});
					return false;
				}
				if (!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.phone))) {
					Toast({
						message: '手机格式错误',
						position: 'middle',
						duration: 1000
					});
					return false;
				}
				if (this.password.length < 6) {
					Toast({
						message: '密码不得少于6位',
						position: 'middle',
						duration: 1000
					});
					return false;
				}
				if (this.verification_code.length < 4) {
					Toast({
						message: '验证码输入有误',
						position: 'middle',
						duration: 1000
					});
					return false;
				}

				this.$http({
					url: this.$store.state.url + '/tool/sendmsgbind',
					method: 'POST',
					params: {
						validateCode: this.verification_code,
						tel: this.phone
					}
				}).then((response) => {
					this.verification_result = response.body;
					if(this.verification_result == 3) {
						Toast({
							message: '验证码输入有误',
							position: 'middle',
							duration: 1000
						});
					}else if(this.verification_result == 2) {
						Toast({
							message: '验证码错误',
							position: 'middle',
							duration: 1000
						});

					} else if(this.verification_result == 7) {
						Toast({
							message: '手机号码已经被注册过',
							position: 'middle',
							duration: 1000
						});

					} else if(this.verification_result == 4) {
						Toast({
							message: '频率过于频繁，请稍候再操作',
							position: 'middle',
							duration: 1000
						});

					} else if(this.verification_result == 1) {
						this.toggle = false;
						this.toggle2 = true;
						var count = 60;
						var _this = this;
						times = setInterval(() => {
							for(var i = 0; i < _this.list.length; i++) {
								Vue.set(_this.list[i], 'time', count);
							}
							count--;
							console.log(count);
							if(count < 0) {
								clearInterval(times);
								this.isActive = false;
								this.blueactive = true;
								console.log(this.isActive)
							}
						}, 1000)
					}

				});

			},
			clickregister() {
				this.$http.get(this.$store.state.url + "/tool/powderSystem/regUser?tel=" + this.phone + "&pwd=" + this.password + "&nickname=" + this.nickname + "&guid=" + this.guid + "&viriCode=" + this.phone_verification

				).then((response) => {
					console.log(response.body + "注册结果");
					if(response.body == -2) {
						Toast({
							message: '用户已经存在，无需注册',
							position: 'middle',
							duration: 1000
						});
					}
					if(response.body == 0) {
						Toast({
							message: '注册失败',
							position: 'middle',
							duration: 1000
						});
					}

					if(response.body > 0) {
						Toast({
							message: '注册成功',
							position: 'middle',
							duration: 1000
						});
						this.toggle = true;
						this.toggle2 = false;


						this.$http.get(this.$store.state.url+"/wx/powderSystem/wxUserbind?tel="+this.phone+
						"&pwd="+this.password+"&openid=" +localStorage.getItem("txq_openId")+
						"&nickname="+ localStorage.getItem("txq_nickname")+
						"&country="+ localStorage.getItem("txq_country")+
						"&province=" +localStorage.getItem("txq_province")+
						"&city="+ localStorage.getItem("txq_city")+
						"&headimgurl=" +localStorage.getItem("txq_headimgurl")+
						"&sex="+ localStorage.getItem("txq_sex")+
						"&unionid="+ localStorage.getItem("txq_unionid")).then((response) =>{
							console.log(response.body)
							console.log('登录');
							if(response.body.errorcode == 404){
								Toast({
												message: '账号密码错误',
												position: 'middle',
												duration: 1000
											});
							}else if(response.body){
								localStorage.setItem("txq_uid", response.body.Id);
								userObj.uid = response.body.Id;
								this.$router.push({path: routerObj.home});
							}
						});
						
						this.list[0].time=0;
								clearInterval(times)
								clearInterval(times2)
						

					}

				})
			},
			resend() {
				this.isActive = true;
				this.blueactive = false;
				this.list[0].time=60
				console.log("1111");
				this.$http({
					url: this.$store.state.url + '/tool/sendmsgbind',
					method: 'POST',
					params: {
						validateCode: this.verification_code,
						        tel: this.phone
					}
				}).then((response) => {

					console.log(response.body);
					this.verification_result = response.body;
					if(this.verification_result == 1) {

						var count = 60;
						var _this = this;
						var is = true
						times2 = setInterval(() => {
							for(var i = 0; i < _this.list.length; i++) {
								Vue.set(_this.list[i], 'time', count);
							}
							count--;
							console.log(count);
							if(count < 0) {
								clearInterval(times2)
								this.isActive = false;
								this.blueactive = true;
							}
						}, 1000)
					} else if(this.verification_result == 4) {
						Toast({
							message: '不要频繁操作',
							position: 'middle',
							duration: 1000
						});
					}

				});

			}

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.register {
		position: relative;
		top: 130px;
		height: calc(100vh - 130px);
		:-ms-input-placeholder {
			color: $pagebg;
			font-size: $font12;
		}
		div.register_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 0;
				margin: 0;
				vertical-align: top;
				height: 50px;
				line-height: 50px;
				width: 70%;
				font-size: $font16;
				padding: 10px;
				box-sizing: border-box;
				/*	 background: #00f;*/
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		div.verification {
			display: flex;
			flex-direction: row;
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin-left: 15%;
			margin-bottom: 15px;
			position: relative;
			input {
				padding: 0 10px;
				border-radius: 2px;
				background-color: $whitecolor;
				border: 1px solid $barbg;
				padding: 0 10px;
				box-sizing: border-box;
				margin-right: 10px;
				width: calc(100% - 100px);
			}
			.verification_img {
				width: 90px;
				height: 50px;
			}
		}
		.nextone {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		.phone_code_page {
			.verification p {
				display: inline-block;
				background-color: $whitecolor;
				text-align: center;
				width: 110px;
				flex: 0 0 110px;
			}
			i {
				font-style: normal;
			}
		}
		.resends {
			pointer-events: none;
			color: $txtcolor;
		}
		.blue {
			color: $bluecolor;
		}
	}
</style>


