<template>
  <div class="examinationResult">
   <p>今日参考人数：<span>{{nowDayData}}</span>
   	<span class="posright" :class="{'greycolor':iscolor}" @click="yicuo">查看易错题<i class="iconfont icon-shuangjiantou-you"></i></span>
   </p>
   <div class="examinationResult_title">
   	<span>昵称</span>
   	<span>正确题数</span>
   	<span>错误题数</span>
   <span @click="getsort">排名
   	<i class='iconfont ' :class="{'icon-jiantou-xia':!isActive,'icon-jiantou-shang': isActive}"></i></span>
   </div>
   <ul class="datalist">
      <li class="on">
	  		<span>{{self.NickName}}</span>
			<span>{{self.OkCount}}</span>
			<span>{{self.NoCount}}</span>
			<span>{{self.Row}}</span>
	  </li>
   </ul>
   <ul class="datalist">
		<li  v-for="(items,index) in datalist">
			<span>{{items.NickName}}</span>
			<span>{{items.OkCount}}</span>
			<span>{{items.NoCount}}</span>
			<span>{{items.Row}}</span>
		</li>
	</ul>
  </div>
</template>

<script>
	import { Toast } from 'mint-ui';
	
export default {
  name: 'examinationResult',
  data () {
    return {
     datalist: [],
     nowDayData:"",
     isActive:false,
     yicuo_count:"",
     iscolor:false,
     self: {}
    }
  },
  methods: {
  	getsort:function(){
  		this.datalist=this.datalist.reverse();
  		this.isActive=!this.isActive;
  	},
  	yicuo() {
  		if(this.yicuo_count==0){
  			Toast({
				message: '今日没有易错题',
				position: 'middle',
				duration: 1000
			});
  		}else {
  			this.$router.push({path: routerObj.errorPractice});
  		}
  	}
  },
  // 实例挂载完毕的回调函数
		mounted() {
			this.$http.get(this.$store.state.url+"/wx/powderSystem/getDayPractiseDataLog").then((response) =>{
				response=response.body;
				console.log(response);
				response.logList.map((v, i) => {
					if (v.Uid == userObj.uid) {
						this.self = v;
					}
				});
				this.datalist=response.logList;
				this.nowDayData=response.nowDayData;
				this.yicuo_count=response.easyerrorlength;
				console.log(this.yicuo_count+"易错题数");
				if(this.yicuo_count=="0"){
					console.log("0");
					this.iscolor=true;
				}
			});

		}
}	
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/css/var.scss'; 
.examinationResult{
	padding-top:90px;
	 
	p{
		position:fixed;
		width:100%;
		height:50px;
		line-height: 50px;
		padding: 0 18px;
		background-color: #fff;
			box-sizing: border-box;
			top:0;
		
		span{ font-size:$font16;color:$bluecolor;}
		.posright{
			position: absolute;
			right: 15px;

		}
		i{
			margin-left: 8px;
			
		}
	}
	.examinationResult_title{
		position: fixed;
		display: -webkit-flex;
		display: flex;
		top: 50px;
		width: 100%;
		text-align: center;
		height: 40px;
		line-height: 40px;
		color: $whitecolor;
		background-color: $txtcolor;
		span{
			flex: 1;
		}
		i{
			font-size: $font14;
			margin-left: 5px;
		}
	}
	.datalist {
   	   box-sizing: border-box;
   	    overflow: scroll;
		li {
			height: 40px;
			display: inline-block;
			display: flex;
			line-height: 40px;
			text-align: center;
			color:$txtcolor;
			background-color: #e5e5e5;
			&.on{
				color: $bluecolor;
				font-weight: bold;
			}
		}
		li:nth-of-type(odd){
			background-color:#f1f1f1 ;
		}
		span {
			flex: 1;
			text-align: center;
		}
	
	}
	.greycolor{
		color: $txtcolor;
		/*pointer-events: none;*/
	}
}

</style>



