<template>
	<div class="enterExamationResult" v-if="isClose">
		<div v-show="isExamationResult">
			<div @click="enter">
				<span class="text" :class="{'underline': examationContent.url}">
					{{examationContent.title}}<br>
				</span>
			</div>
			<span v-if="show == 2" class="close" @click.prev="closeExamationResult">
				<i class="iconfont icon-close01"></i>
			</span>
		</div>
		<span class="switch" :class="{'right0': isRight}" @click="arrowSwitch">
			<i class="iconfont" :class="{'icon-shangla': isExamationResult, 'icon-xiala': !isExamationResult}"></i>
		</span>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';

export default {
	name: 'enterExamationResult',
	props: ['list'],
	data () {
		return {
			// 是否整体关闭
			isClose: true,
			// 是否靠右
			isRight: false,
			// 是否显示关闭按钮
			show: 1,
			// 是否显示公告
			isExamationResult: true,
			examationContent: {
				id: '',
				title: '',
				url: ''
			}
		}
	},
	watch: {
		list(val) {
			console.log('更新了？');
			this.isExamationResult = true;
			this.examationContent = val;
		}
	},
	mounted() {

		// 是否显示关闭按钮
		setTimeout(() => {
			if (userObj.adminId == 2) {
				this.show = userObj.adminId;
				this.isRight = false;
			} else {
				this.show = userObj.adminId;
				this.isRight = true;
			}
		},500)


		// 查询公告是否过期
		


		// 获取公告
		this.$http.get(`${this.$store.state.url}/wx/powderSystem/getNoticeListByGroup?groupId=${userObj.groupId}`).then(res => {
			// 有数据的情况下才显示公告
			if (res.body && res.body.Id != undefined) {
				this.examationContent.id = res.body.Id;
				this.examationContent.title = res.body.NoticeTitle;
				this.examationContent.url = res.body.NoticeUrl;
				let examation = {
					id: this.examationContent.id
				}
				// 存储当前关闭公告id
				localStorage.setItem('examation', JSON.stringify(examation));
			} else {
				this.examationContent.title = '暂无公告';
				this.show = 1;
				this.isExamationResult = false;
			}
			this.isExamationResult = true;
			},error => {

		});
	},

	methods: {
		// 展开收起
		arrowSwitch() {
			if (this.isExamationResult) {
				this.isExamationResult = false;
				this.isRight = true; 
			} else {
				this.isExamationResult = true;
				if (this.show == 2) {
					this.isRight = false; 
				} else {
					this.isRight = true; 
				}	
			}
		},
		// 进入公告详情
		enter() {
			// this.$router.push({path: routerObj.examinationResult});
			if (this.examationContent.url) {
				location.href = this.examationContent.url;
			}
		},
		// 关闭公告
		closeExamationResult() {

			MessageBox.confirm('确定删除公告？').then(action => {
				// 整体关闭
				this.isClose = false;
				if (localStorage.getItem('examation') != null) {
					let id = JSON.parse(localStorage.getItem('examation')).id;
					this.$http({
						url: `${this.$store.state.url}/wx/powderSystem/setNoticeSkipTime`,
						method: 'GET',
						params: {
							noticeId:id,
							state: 1
						}
					}).then(res => {
						console.log(res);
						if (res.body > 0) {
							this.show = 1;
							this.isRight = true;
						}
					},err => {
						
					});
				}
			}, rejected => {
				// 取消
			});

			
		}

	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.enterExamationResult{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 23px;
	& > div {
		padding: 10px;
		width: 100%;
		box-sizing: border-box;
		background: rgba(0,0,0,.6);
	}
	.text{
		color: #fff;
		font-size: $font16;
		@extend %textHidden3;
		&.underline{
			text-decoration: underline;
		}
	}
	.close,.switch{
		width: 23px;
		height: 23px;
		line-height: 23px;
		background: rgba(0,0,0,.9);
		text-align: center;
		.iconfont{
			color: #fff;
		}
	}
	.close{
		position: absolute;
		right: 0;
		bottom: 0;
	}
	.switch{
		position: absolute;
		right: 23px;
		bottom: 0;
	}
	.right0{
		right:0;
	}

}
</style>



