<template>
	<div class="banned">
		<!--主页-->
		<div class="list">

			<p>禁言名单</p>
			<ul>
				<!---->
				<li class="bannedList_li" v-for="item in infoList" @touchstart="sliderStart" @touchmove="sliderMove">
					<a href="#" ref="listA">
						<span class="avator">
						<img :src="item.headimgurl" /></span>
						<span class="user_name">{{item.nickname}}</span>
						<i @click="delbanned(item.Uid)" :data-uid="item.Uid">删除</i>
					</a>
				</li>

			</ul>
		</div>
		<!--弹框-->
		<div class="banned_mask" v-if="toggle">
			<div>
				<p>确定要从禁言名单中</p>
				<p>移除该学员吗</p>
			</div>
			<p class="displayflex"><span @click="clickcancel">取消</span>
				<span class="sure" @click="clicksure">确认</span>
			</p>
		</div>
		<!--底部-->
		<div class="banned_bottom" @click="totalbanned">
			<p v-if="chooselbanned">全部禁言</p>
			<p v-else>取消全部禁言</p>
		</div>

		<v-mask :flag="toggle"></v-mask>
	</div>
</template>

<script>
	// 引用遮罩层组件
	import mask from '../functionComponents/mask'

	// websocket
	import Paho from '@/assets/js/websocket'

	const ERR_Ok = 0;

	export default {
		name: 'banned',
		data() {
			return {
				toggle: false,
				chooselbanned: true,
				infoList: [],
				uid: "",
				state:"",
				sliderObj: {
					 //是否存在展开的list
					expansion: null,
					x: '',
					y: '',
					X: '',
					Y: '',
					swipeX: '',
					swipeY: ''
				}
				
			}
		},

		// 实例挂载完毕的回调函数
		mounted() {



			// 设置页面title
			document.title = '禁言列表';

			this.MQTTconnect();

			//获取禁言用户列表
			this.$http.get(this.$store.state.url+'/wx/powderSystem/getIsProhibitUserList?groupId='+userObj.groupId).then((response) => {
				response = response.body;
				this.infoList = response;
			});

			// 获取禁言状态
			this._ajax(`${this.$store.state.url}/wx/powderSystem/getUserByMessageState`,'GET',{
				uid: userObj.uid,
				openId: userObj.openId,
				groupId: userObj.groupId,
			}, (res) => {
				if (res.body.groupBanned) {
					this.chooselbanned = false;
				} else {
					this.chooselbanned = true;
				}
				this.$store.commit('setGroupBanned', res.body.groupBanned);
				this.$store.commit('setSelfBanned', res.body.selfBanned);
			});

		},
		components: {
			'v-mask': mask,
		},
		methods: {
			// 初始化 websocket
			MQTTconnect(cb) {
				mqtt = new Paho.MQTT.Client(
					host,//MQTT域名
					port,//WebSocket端口
					clientId//客户端ClientId
				);
				let message;
				var options = {
					timeout: 3,
					onSuccess: () => {
						// Connection succeeded; subscribe to our topic
						mqtt.subscribe(topic, {qos: 0});
						message = new Paho.MQTT.Message(JSON.stringify(onlineMessage));//set body
						console.log(message);
						message.destinationName = topic;// set topic
						mqtt.send(message);
						cb && cb();
					},
					onFailure: (message) => {
						console.log(JSON.stringify(message));
						setTimeout(this.MQTTconnect, reconnectTimeout);
					}
				};
				mqtt.onConnectionLost = (response) => {
					setTimeout(this.MQTTconnect, reconnectTimeout);
				};
				mqtt.onMessageArrived = (message) => {
					this.MessageArrived(message.destinationName, message.payloadString);
				};
				if (username != null) {
					options.userName = username;
					options.password = password;
				}
				mqtt.connect(options);
			},
			// 发送websocket
			MessageSend(txt) {
				// Connection succeeded; subscribe to our topic
				mqtt.subscribe(topic, {qos: 0});
				var message = new Paho.MQTT.Message(txt);//set body
				message.destinationName = topic;// set topic
				mqtt.send(message);
			},
			// 接收websocket
	  		MessageArrived(top, txt) {
				console.log(txt);
				try {
					let s = JSON.parse(txt);
					if (s.Uid) {
						let flag = true;
						//  禁言
						this.infoList.map((v,i) => {
							if (v.Uid == s.Uid) {
								flag = false;
								return;
							}
						});
						if (flag) {
							if (s.dataType == 4 && !s.cancel) {
								this.infoList.push(s);
							}	
						}
						
					}
				}catch(err) {

				}
			},
			// 开始
			sliderStart(event) {
				this.sliderObj.x = event.changedTouches[0].pageX;
				this.sliderObj.y = event.changedTouches[0].pageY;
				this.sliderObj.swipeX = true;
				this.sliderObj.swipeY = true;
				if(this.sliderObj.expansion) { //判断是否展开，如果展开则收起
					this.sliderObj.expansion.className = "";
				}
			},
			// 移动
			sliderMove(event) {
				this.sliderObj.X = event.changedTouches[0].pageX;
				this.sliderObj.Y = event.changedTouches[0].pageY;
				// 左右滑动
				if(this.sliderObj.swipeX && Math.abs(this.sliderObj.X - this.sliderObj.x) - Math.abs(this.sliderObj.Y - this.sliderObj.y) > 0) {
					// 阻止事件冒泡
					event.stopPropagation();
					if(this.sliderObj.X - this.sliderObj.x > 10) { //右滑
						event.preventDefault();
						if (event.target.localName == 'a') {
							$(event.target).removeClass('swipeleft');//右滑收起
						}
					}
					if(this.sliderObj.x - this.sliderObj.X > 10) { //左滑
						event.preventDefault();
						if (event.target.localName == 'a') {
							$(event.target).addClass('swipeleft');//左滑展开
						}
						this.sliderObj.expansion = this;
					}
					this.sliderObj.swipeY = false;
				}
				// 上下滑动
				if(this.sliderObj.swipeY && Math.abs(this.sliderObj.X - this.sliderObj.x) - Math.abs(this.sliderObj.Y - this.sliderObj.y) < 0) {
					this.sliderObj.swipeX = false;
				}
			},
			clicksure: function() {
				this.toggle = !this.toggle;
				this.$http.get(this.$store.state.url+'/wx/powderSystem/cancelIsProhibitUser?groupId='+userObj.groupId+'&uid=' + this.uid).then((response) => {
					response = response.body;
					console.log(this.infoList);
					console.log('this.uid = ' + this.uid);
					for(let i in this.infoList) {
						if(this.infoList[i].Uid == this.uid) {
							console.log('进来了');
							this.infoList.splice(i, 1);
							let obj = {
								Uid: this.uid,
								dataType: 4,
								cancel: true
							};
							this.MessageSend(JSON.stringify(obj));
						}
					}
				})
			},
			clickcancel: function() {
				this.toggle = !this.toggle;
			},
			delbanned: function(uid) {
				event.preventDefault();
				this.toggle = !this.toggle;
				this.uid = uid;
				console.log(this.uid);
			},
			totalbanned: function() {
				this.chooselbanned = !this.chooselbanned;
				if(this.chooselbanned==true){
					this.state=0;
					this.$http.get(this.$store.state.url+"/wx/powderSystem/edGroupIsProhibitState?groupId="+userObj.groupId+"&state="+this.state).then((response) => {
						console.log(response.body);
						// 取消全体禁言
						let obj = {
							Uid: userObj.uid,
							banned: false,
							dataType: 44
						};
						this.MessageSend(JSON.stringify(obj));
					});
				}else{
					this.state=1;
					this.$http.get(this.$store.state.url+"/wx/powderSystem/edGroupIsProhibitState?groupId="+userObj.groupId+"&state="+this.state).then((response) => {
						console.log(response.body);
						let obj = {
							Uid: userObj.uid,
							banned: true,
							dataType: 44
						};
						this.MessageSend(JSON.stringify(obj));
						console.log('全体禁言');
					});
				}
			},
			// ajax 请求
			_ajax(url, type, data, cb) {
				this.$http({
				url: url,
				method: type,
				params: data
				})
				.then(res => {
				if(res.status === 200) {
					cb && cb(res);
				}
				}, error => {
				console.log(error);
				});
			}

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.bannedList_li {
		img {
			height: 40px;
			width: 40px;
			vertical-align: middle;
		}
	}
	
	.list {
		overflow: hidden;
		border-top: 1px solid #ddd;
		.user_name {
			margin-left: 8px;
		}
		span {
			vertical-align: middle;
		}
		p {
			height: 25px;
			line-height: 25px;
			margin-left: 10px;
		}
		i {
			font-style: normal;
		}
		ul {
			border-top: 1px solid $bordercolor;
			background-color: $whitecolor;
		}
		li {
			overflow: hidden;
			width: 100%;
			border-bottom: 1px solid #ddd;
			height: 60px;
			line-height: 60px;
			.avator{
			margin-left: 15px;
			}
		}
		a {
			display: block;
			height: 60px;
			color: $txtcolor;
			transition: all 0.3s linear;
		}
		i {
			float: right;
			margin-right: -80px;
			width: 80px;
			text-align: center;
			background: $bordercolor;
			color: #fff;
			text-align: center;
			
		}
		.swipeleft {
			transform: translateX(-80px);
		}
	}
	
	.banned_mask {
		width: 70%;
		height: 140px;
		background-color: #fff;
		border-radius: 4px;
		position: fixed;
		top: 50%;
		left: 50%;
		z-index: 11;
		padding-top: 25px;
		text-align: center;
		box-sizing: border-box;
		transform: translate(-50%, -50%);
		p {
			height: 22px;
			line-height: 22px;
		}
		div {
			border-bottom: 1px solid $bordercolor;
			padding-bottom: 25px;
		}
		.displayflex {
			display: flex;
			height: 45px;
			line-height: 45px;
			margin-right: -1px;
			span {
				flex: 1;
			}
			span.sure {
				border-left: 1px solid $bordercolor;
				color: $bluecolor;
			}
		}
	}
	
	.banned_bottom {
		position: fixed;
		width: 100%;
		bottom: 0;
		height: 55px;
		line-height: 55px;
		color: $whitecolor;
		background-color: $txtcolor;
		font-size: $font16;
		text-align: center;
	}
</style>


