import Vue from 'vue'
import Router from 'vue-router'
// 登录-路由
import login from '@/components/login/login'
// 注册-路由
import register from '@/components/register/register'
// 聊天主页-路由
import home from '@/components/home/home'
// 聊天主页-设置-路由
import homeSet from '@/components/home/children/set'
// 任务编辑-路由
import taskEdit from '@/components/taskEdit/taskEdit'
// 禁言-路由
import banned from '@/components/banned/banned'
// 报表-路由
import statement from '@/components/statement/statement'
// 邀请好友-路由
import inviteFriends from '@/components/inviteFriends/inviteFriends'
// 每日一练结果-路由
import examinationResult from '@/components/examinationResult/examinationResult'
// 易错题-路由
import errorPractice from '@/components/errorPractice/errorPractice'

Vue.use(Router)

Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}


export default new Router({
  // HTML5 History 模式
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: routerObj.login
    },
    {
      path: '*',
      component: login
    },
    {
    	path: routerObj.login,
    	component: login
    },
    {
    	path: routerObj.register,
    	component: register
    },
    {
    	path: routerObj.home,
    	component: home,
      children: [
        	{
            path: routerObj.homeSet,
    	      component: homeSet
          }
      ]
    },
    {
    	path: routerObj.taskEdit,
    	component: taskEdit
    },
    {
    	path: routerObj.banned,
    	component: banned
    },
    {
    	path: routerObj.statement,
    	component: statement
    },
    {
    	path: routerObj.inviteFriends,
    	component: inviteFriends
    },
    {
    	path: routerObj.examinationResult,
    	component: examinationResult
    },
    {
    	path: routerObj.errorPractice,
    	component: errorPractice
    }
  ]
})



