import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		// 使用vuex保存当前用户身份 1 为普通用户   2 为 管理员
		adminId: userObj.adminId,
		// 聊天对象
		practiceObj:{
			// 进入时候的数据
			enterDataList:[],
			// 消息数组对象
			selfSendPracticeList:[],
			// 签到
			signIn: false,
			// 全局禁言
			groupBanned: bannedObj.groupBanned,
			// 当前自己是否禁言
			selfBanned: bannedObj.selfBanned,
			// 是否开启录播
			isRecorded: false
		},
		// 保存路由对象
		routerObj,
		// 保存当前请求地址
		url: userObj.ajaxUrl
	},
	mutations: {
		setGroupBanned (state, val) {
			state.practiceObj.groupBanned = val;
		},
		setSelfBanned (state, val) {
			state.practiceObj.selfBanned = val;
		},
		setRecorded(state, val) {
			state.practiceObj.isRecorded = val;
		}
	}
}) 




