<template>
	<div class="taskActive">
		<div class="onlive">
			<label for="title">标题：
   	 	<input type="text" id="title" class="title" v-model.trim="titletxt"/>
   	 </label>
			<label for="link">链接：
   	 	<input type="url" id="link" v-model.trim="linktxt"/>
   	 </label>
		</div>
		<div class="choosecheck">
			<input type="checkbox" id="service" value="service" v-model="checkedservice">
			<label for="service">服务号</label>
			<input type="checkbox" id="group" value="group" v-model="checkedgroup">
			<label for="group">群系统</label>

		</div>
		<button class="sendButton" :disabled="isClick" @click="send">确认发送</button>

	</div>
</template>

<script>
	
	import Vue from "vue"
	import { Toast } from 'mint-ui';
	export default {
	
		name: 'taskActive',
		data() {
			return {
				linktxt: "",
				titletxt: "",
				checkedservice: true,
				checkedservice_num: "",
				checkedgroup: true,
				checkedgroup_num: "",
				isClick: false
			}
		},
		methods: {
			send: function() {
				if(this.linktxt == "" || this.titletxt == "") {
					Toast({
						message: '请输入内容',
						position: 'middle',
						duration: 1000
					});
				} else {
					this.checkedgroup_num = this.checkedgroup ? 1 : 0;
					this.checkedservice_num = this.checkedservice ? 1 : 0;
					this.isClick = true;
					this._get(`${this.$store.state.url}/wx/powderSystem/addNoticeInfo`, {
						title: this.titletxt,
						url: this.linktxt,
						type: 3,
						groupId: userObj.groupId,
						adminId: userObj.adminId,
						sendClient: this.checkedgroup_num,
						sendServer: this.checkedservice_num
					}, (res) => {
						if (res.status === 200) {
							Toast({
								message: '操作成功',
								position: 'middle',
								duration: 1000
							});
							let obj = {};
							if (res.body) {
								obj = {
									id: res.body,
									title: this.titletxt,
									url: this.linktxt,
									dataType: 7
								};
							}
							
							this.$router.push({path: routerObj.home});
							this.$emit('activeSend',obj)
							//  清空
							setTimeout(() => {
								this.clearData();
							},500);
						}
					});
				}

			},
			// 清空
			clearData() {
				this.titletxt = '';
				this.linktxt = '';
			},
			_get(url, data, cb) {
				this.$http({
					url: url,
					method: 'GET',
					params: data
				}).then( res => {
					cb && cb(res);
				}, err => {
					console.log(err);
				});
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.onlive {
		border: 1px solid $bordercolor;
		color: $txtcolor;
		padding: 0 $font16;
		background-color: $whitecolor;
		label {
			display: block;
			
			font-size: $font14;
		}
		input {
			border: none;
			font-size: $font14;
			width: 80%;
			padding:20px 0;
			margin: 0;
			
		}
		[for="title"] {
			border-bottom: 1px solid $bordercolor;
		}
	}
	
	.sendButton {
		display: block;
		height: 50px;
		line-height: 50px;
		color: #fff;
		text-align: center;
		font-size: $font18;
		background-color: $bluecolor;
		width: 90%;
		margin: 0 auto;
		margin-top: 20px;
		border-radius: 3px;
	}
	.choosecheck {
		height: 40px;
		line-height: 40px;
		margin-left: 5%;
	}
</style>


