<template>
	<div class="inviteFriends">
		<!--<span class="delicon">×</span>-->
		<p>长按发送给朋友</p>
		<p>邀请好友一起组队学习</p>
		<div class="pos_relative">
			<img :src="this.info.ShareImg" />

		</div>
	</div>
</template>

<script>
	export default {
		name: 'inviteFriends',
		data() {
			return {
				info: {},
				id: "",
				_uid: ""
			}
		},
		// 实例挂载完毕的回调函数
		mounted() {
			var test=window.location.href;
			if(test.indexOf("uid") == -1) {
				this._uid = userObj.uid;
			} else {
				var place = parseInt(test.indexOf("uid") + 4);
				test = test.substring(place,test.length);
				console.log(test)
				this._uid = test;
			}
			this.$http.get(`${this.$store.state.url}/wx/powderSystem/getUserJoinImage?uid=${this._uid}&groupId=${userObj.groupId}`).then((response) => {
				response = response.body;
				this.info = response;
				console.log(this.info.ShareImg);
			});
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.inviteFriends {
		height: 100vh;
		overflow: hidden;
		position: relative;
		padding-top: 10%;
		.delicon {
			font-size: 45px;
			position: absolute;
			right: 15px;
			top: 0px;
		}
		p {
			color: $bluecolor;
			text-align: center;
			height: 22px;
			line-height: 22px;
		}
		img {
			width: 80%;
			box-shadow: 0px 4px 6px $iconcolor;
			margin-left: 10%;
		}
		.pos_relative {
			position: relative;
			margin-top: 20px;
		}
	}
</style>


