<template>
	<div class="taskEdit" :type="type">
		<div class="taskEdit_List">
			<!--箭头 表名 arrowdown -->
			<p @click="toggle = !toggle">{{activename}}
				<span :class="{'arrowdown':!toggle,'arrowup': toggle}"></span></p>
			<ul v-if="toggle">
				<li v-for="(item,index) in items" @click="selectStyle(item,index)"
					 :class="{'active':item.active,'unactive':!item.active}" >
					{{item.select}}
				</li>
			</ul>
		</div>
		<component :is="com" @announcementSend="_websocketSend" @activeSend="_websocketSend" @liveVideoSend="_websocketSend"></component>
		
		<v-mask :flag="toggle"></v-mask>
	</div>
</template>

<script>
	import Vue from 'vue'
	
	import { Checklist } from 'mint-ui';
	// 引用遮罩层组件
	import mask from '../functionComponents/mask'
	// 引用普通公告组件
	import announcement from '../functionComponents/taskAnnouncement'
	// 引用视频直播组件
	import liveVideo from '../functionComponents/taskliveVideo'
	// 引用外部活动组件
	import active from '../functionComponents/taskActive'

	// websocket
	import Paho from '@/assets/js/websocket'
	export default {
		name: 'taskEdit',
		data() {
			return {

				items: [　　
					{
						select: '普通公告',
						component: 'announcement',
						type:1
					}, 　　　　　　　　　　 
					{
						select: '直播视频',
						component: 'liveVideo',
						type:2
					}, 　　　　　　　　　　
					 {
						select: '外部活动',
						component: 'active',
						type:3
					}　　　　　　　　　　　　　　　　　
				],
				activename: "直播视频",
				toggle: false,
				com: 'liveVideo',
				type:""
			}
		},
		mounted() {
			// 连接websocket
			this.MQTTconnect();
		},
		methods: {
			// 初始化 websocket
			MQTTconnect(cb) {
				mqtt = new Paho.MQTT.Client(
					host,//MQTT域名
					port,//WebSocket端口
					clientId//客户端ClientId
				);
				let message;
				var options = {
					timeout: 3,
					onSuccess: () => {
						// Connection succeeded; subscribe to our topic
						mqtt.subscribe(topic, {qos: 0});
						message = new Paho.MQTT.Message(JSON.stringify(onlineMessage));//set body
						console.log(message);
						message.destinationName = topic;// set topic
						mqtt.send(message);
						cb && cb();
					},
					onFailure: (message) => {
						console.log(JSON.stringify(message));
						setTimeout(this.MQTTconnect, reconnectTimeout);
					}
				};
				mqtt.onConnectionLost = (response) => {
					setTimeout(this.MQTTconnect, reconnectTimeout);
				};
				mqtt.onMessageArrived = (message) => {
					this.MessageArrived(message.destinationName, message.payloadString);
				};
				if (username != null) {
					options.userName = username;
					options.password = password;
				}
				mqtt.connect(options);
			},
			// 发送websocket
			MessageSend(txt) {
				// Connection succeeded; subscribe to our topic
				mqtt.subscribe(topic, {qos: 0});
				var message = new Paho.MQTT.Message(txt);//set body
				message.destinationName = topic;// set topic
				mqtt.send(message);
			},
			// 接收websocket
	  		MessageArrived(top, txt) {
				console.log(txt);
				try {
					let s = JSON.parse(txt);
					if (s.Uid) {
						//  禁言
						if (s.dataType == 4) {
							this.infoList.push(s);
						}
					}
				}catch(err) {

				}
			},
			// 普通公告发送
			_websocketSend (obj) {
				if (obj) {
					// 发送websocket
					this.MessageSend(JSON.stringify(obj));
				}
			},

			// 切换公告类型
			selectStyle: function(item, index) {
				this.activename = this.items[index].select;
				this.toggle = !this.toggle;
				this.com = this.items[index].component;
				this.type = this.items[index].type;
				console.log(this.type);
				this.items.forEach(function (item) {
　　　　　　　　　　Vue.set(item,'active',false);
　　　　　　　　});
　　　　　　　　Vue.set(item,'active',true);
			}

		},
		components: {
			'v-mask': mask,
			announcement,
			liveVideo,
			active
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.taskEdit_List {
		height: 45px;
		line-height: 45px;
		width: 100vw;
		text-align: center;
		background-color: $whitecolor;
		margin-bottom: 10px;
		position: relative;
		z-index: 10;
		p {
			border-bottom: 1px solid $bordercolor;
			position: relative;
			font-size: $font16;
			width: 100%;
			.arrowdown:after {
				content: "";
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
				border-top: 8px solid $txtcolor;
				position: absolute;
				top: 18px;
				margin-left: 5px;
			}
			.arrowup:after {
				content: "";
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
				border-bottom: 8px solid $txtcolor;
				position: absolute;
				top: 18px;
				margin-left: 5px;
			}
		}
		ul {
			background-color: $whitecolor;
		}
		li {
			height: 44px;
			line-height: 44px;
			border-bottom: 1px solid $pagebg;
			margin: 0 20px;
			font-size: $font14;
		}
		
		.taskEdit_List_active {
			color: $bluecolor;
		}
		.active {
			color: #1c91d4;
		}
		.unactive {
			color: #464646;
		}
	}
</style>


