<template>
  <div class="set">
    <ul>
      <li>接收做题通知<mt-switch v-model="switchTitle"></mt-switch></li>
      <li>接收签到通知<mt-switch v-model="switchSign"></mt-switch></li>
    </ul>
    <ul>
      <li @click="openNotice = !openNotice">
        消息通知设置 <span class="span">{{noticeNumber}}</span><i class="iconfont icon-202-copy"></i>
      </li>
    </ul>
    <mt-actionsheet :actions="noticeArr" v-model="openNotice"></mt-actionsheet>
  </div>
</template>

<script>
// 开关控件、操作表
import { Switch,Actionsheet  } from 'mint-ui';

export default {
  name: 'msgTime',
  props: ['time'],
  data () {
    return {
      // 接收做题通知
      switchTitle: false,
      // 接收签到通知
      switchSign: false,
      // 是否打开通知设置
      openNotice: false,
      // 默认接收条数
      noticeNumber: '5条',
      // 通知设置
      noticeArr: [
        {
          name: '在达到选择的通知条数时，将给你发出新消息通知',
          method: ''
        },
        {
          name: '5条',
          method: this.setMsgCount
        },
        {
          name: '10条',
          method: this.setMsgCount
        },
        {
          name: '50条',
          method: this.setMsgCount
        },
        {
          name: '100条',
          method: this.setMsgCount
        },
        {
          name: '不接收通知',
          method: this.setMsgCount
        }
      ]
    }
  },
  mounted() {

    // 获取当前推送
    this._ajax(`${this.$store.state.url}/wx/powderSystem/getUserPushSetting`,'GET', {
      uid: userObj.uid,
      openId: userObj.openId,
      groupId: userObj.groupId
    }, (res) => {
      console.log(res);
      // 试题通知
      this.switchTitle = !!parseInt(res.body.IsQuestionMakSend, 10);
      // 签到通知
      this.switchSign = !!parseInt(res.body.IsSignMakeSend, 10);
      // 通知条数 
      this.noticeNumber = !!parseInt(res.body.IsSignMakeSend, 10) ? res.body.MakeSendCount + '条' : '不接收通知';
    });
  },
  methods: {
    // 设置当前消息条数
    setMsgCount(val) {
      this.noticeNumber = val.name
    },
    // ajax 请求
    _ajax(url, type, data, cb) {
      this.$http({
        url: url,
        method: type,
        params: data
      })
      .then(res => {
        if(res.status === 200) {
          cb && cb(res);
        }
      }, error => {
        console.log(error);
      });
    }
  },
  watch: {
    // 监控试题通知的变化
    switchTitle(val) {
      let value = val ? 1 : 0;
      this._ajax(`${this.$store.state.url}/wx/powderSystem/edUserPushInfo`,'GET', {
        uid: userObj.uid,
        openId: userObj.openId,
        groupId: userObj.groupId,
        IsQuestionMakeSend: value
      });
    },
    // 监控签到通知的变化
    switchSign(val) {
      let value = val ? 1 : 0;
      this._ajax(`${this.$store.state.url}/wx/powderSystem/edUserPushInfo`,'GET', {
        uid: userObj.uid,
        openId: userObj.openId,
        groupId: userObj.groupId,
        IsSignMakeSend: value
      });
    },
    // 监控通知条数的变化
    noticeNumber(val) {
      let value = parseInt(val, 10);
      if (isNaN(value)) {
        value = 0;
      }
      this._ajax(`${this.$store.state.url}/wx/powderSystem/edUserPushInfo`,'GET', {
        uid: userObj.uid,
        openId: userObj.openId,
        groupId: userObj.groupId,
        MakeSendCount: value
      });
    }
  },
  components: {
    'mt-switch': Switch,
    'mt-actionsheet': Actionsheet
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../../assets/css/var.scss";
.set{
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: #f1f1f1;
  ul{
      margin-top: 20px;
      padding: 0 10px;
      background: #fff;
      border-top: 1px solid $bordercolor;
      border-bottom: 1px solid $bordercolor;
  }
  li{
    position: relative;
    padding: 15px 0;
    border-bottom: 1px solid $bordercolor;
    .span{
      position: absolute;
      right: 20px;
      top: 18px;
      font-size: $font12;
      color: #999;
    }
    .iconfont{
      position: absolute;
      right: 0;
      top: 16px;
      color: #999;
    }
    .mint-switch{
      position: absolute;
      right: 0;
      top: 10px;
    }
    &:last-child{
      border-bottom: none;
    }
  }
}
</style>



