<template>
	<div class="login">
		<div class="login_flex">
			<div class="login_div">
			<i class="iconfont icon-shouji"></i>
			<input type="tel" placeholder="请输入手机号" v-model.trim="phone" maxlength="11" />
		</div>
		<div class="login_div">
			<i class="iconfont icon-mima"></i>
			<input type="password" placeholder="请输入密码" v-model.trim="password" />
		</div>
		<p class="login_button" @click="login">登录</p>
		</div>
		<div class="reg">没有账号？
			<router-link :to="{path: $store.state.routerObj.register }">马上注册</router-link>
		</div>
	</div>

</template>

<script>
	import { Toast } from 'mint-ui';
	
	export default {
		name: 'login',
		data() {
			return {
				phone: "",
				password: ""
			}
		},
		mounted() {
			//读取本地微信openid 有的话直接跳转 进入首页home
			//没有的话 请求获取openid 登录页面  信息包括openid一起提交服务器
			//var wxopenid="oWk-fuP7uqLUJcoX118Mt6oWyFZo";

			// localStorage.setItem("openId" ,"oWk-fuP7uqLUJcoX118Mt6oWyFZo");
			
			var wxopenid = localStorage.getItem("txq_openId");
			var uid = localStorage.getItem('txq_uid');

			if(wxopenid == undefined || wxopenid == '') {
				//假装获取 到 openid  

				// var wxopenid = "oyecBuKbF1seYxcWQOW5HapiKVHw";
				//如果为空 跳转登录页面

				console.log("跳转登录页面");
				location.href = userObj.loginUrl;

			}
			if(uid == null) {
				this.$http.get(this.$store.state.url+"/wx/powderSystem/virUserByOpenId?openid=" + wxopenid).then((response) => {					
					//成为全局使用
					if (response.body.uid == undefined) {
						this.$router.push({path: routerObj.login});
					} else {
						if (response.body.uid != 0) {
							let self = this;
							localStorage.setItem("txq_uid", response.body.uid);
							userObj.nickname=response.body.nickname;
							userObj.uid = response.body.uid;
							userObj.headimgurl=response.body.headimgurl;
							this.$router.push({path: routerObj.home});
						}
					}
					
				});
			} else {
				this.$router.push({path: routerObj.home});
			}

			function isEmptyObject(obj) {
				for(var key in obj) {
					return false;
				}
				return true;
			}
		},
		methods: {
			login() {
//				http://120.24.6.91:8082/wx /powderSystem/wxUserbind
//
//	tel				手机号码	
//	pwd				密码
//	openid			OpenId
//	nickname			微信昵称
//	country			市级
//	province			省级
//	city				县
//	headimgurl		头像地址
//	sex				性别
//	unionid			(先空)

if (!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.phone))) {
	Toast({
		message: '手机格式错误',
		position: 'middle',
		duration: 1000
	});
	return false;
}
if (this.password.length < 6) {
	Toast({
		message: '密码不得少于6位',
		position: 'middle',
		duration: 1000
	});
	return false;
}

this.$http.get(this.$store.state.url+"/wx/powderSystem/wxUserbind?tel="+this.phone+
"&pwd="+this.password+"&openid=" +localStorage.getItem("txq_openId")+
"&nickname="+ localStorage.getItem("txq_nickname")+
"&country="+ localStorage.getItem("txq_country")+
"&province=" +localStorage.getItem("txq_province")+
"&city="+ localStorage.getItem("txq_city")+
"&headimgurl=" +localStorage.getItem("txq_headimgurl")+
"&sex="+ localStorage.getItem("txq_sex")+
"&unionid="+ localStorage.getItem("txq_unionid")).then((response) =>{
	console.log(response.body)
	console.log('登录');
	if(response.body.errorcode == 404){
		Toast({
			message: '账号密码错误',
			position: 'middle',
			duration: 1000
		});
	}else if(response.body){
		localStorage.setItem("txq_uid", response.body.Id);
		userObj.uid = response.body.Id;
		this.$router.push({path: routerObj.home});
	}
});

			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.login {
		display:flex;
		flex-direction: column;
		height:100vh;
		justify-content: space-between;
	.login_flex{
	    margin-top: 15vh;}
		div.login_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 10px 0;
				height:50px;
				line-height: 50px;
				margin: 0;
				vertical-align:top;
				box-sizing: border-box;
				width: 70%;
				font-size: $font14;
					 /*background: #00f;*/
			}
			 :-ms-input-placeholder {
				color: $pagebg;
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		p.login_button {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		div.reg {
				height: 50px;
				line-height: 50px;
				text-align: center;
			i {
				color: $bluecolor;
				text-decoration: underline;
			}
		}
	}
</style>


