      bannedText: '已被禁言',
      // 每次保存第一条历史消息id
      historyMsgId: false,
      englishOption: ['A', 'B', 'C', 'D', 'E', 'F', 'G'],
      // 长按
      timeOutEvent: 0,
      // 撤回
      withdrawArr: [],
      // 遮罩
      mask: false,
      // 签到
      signInArr: {},
      state:3
    }
  },
  // 进入该路由时调用
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$nextTick(() => {
        setTimeout(() => {
          // 滚动到底部
          util.partScrollToBottom();
        },400);
      });
