  },
  created() {
    // 开启首屏加载
    // this.mask = true;
    // Indicator.open({
    //  text: '正在连接中...',
    //  spinnerType: 'fading-circle'
    // });
  },
  mounted() {


   // 设置adminId
	 setTimeout(() => {
        this.show = userObj.adminId;
    },500);

    // 如果没有登录就跳转回登录也
    if(localStorage.getItem("txq_uid") == null || localStorage.getItem("txq_openId") == null) {
      this.$router.push({
        path: routerObj.login
      });
    }

    // 初始化
    this._init();

  
    

  },
  methods: {

  // 初始化
  _init() {


    let doc = document.body || document.documentElement;
    // 全局回车事件
    doc.addEventListener('keydown', (event) => {
      if(event.keyCode === 13) {
        if(this.chatInput) {
          // 发送
          this._send();
        }
      }
    }, false);

    // 聊天区域点击事件
    this.$refs.loadmore.$el.addEventListener('touchstart', () => {
      this.isMoreFunction = false;
      this.hideWithDraw();
    }, false);

    // 滚动触发时，隐藏长按显示的信息
    this.$refs.loadmore.$el.addEventListener('scroll', () => {
      this.hideWithDraw();
      let con = $('.home-top');
      if (con.scrollTop() > ($('.mint-loadmore-content').outerHeight(true) - con.height() - 200)) {
        this.isNewMsg = false;
      }
    }, false);

    // 获取签到状态
    if(localStorage.getItem('signInArr') != null) {
      this.signInArr = JSON.parse(localStorage.getItem('signInArr'));
    } else {
        this._ajax(`${this.$store.state.url}/wx/powderSystem/getEndSignLog`, 'GET', {
          uid: userObj.uid,
          groupId: userObj.groupId
        }, (res) => {
          if (res.body.Id &&  res.body.Id != undefined) {
              this.signInArr.id = res.body.Id;
              this.signInArr.show = res.body.userMakeState == 0 ? true : false;
          } else {
            localStorage.removeItem('signInArr');
          }
        });
    }

    // 获取禁言状态
    this._ajax(`${this.$store.state.url}/wx/powderSystem/getUserByMessageState`,'GET',{
      uid: userObj.uid,
      openId: userObj.openId,           
      groupId: userObj.groupId
    }, (res) => {
      this.$store.commit('setGroupBanned', res.body.groupBanned);
      this.$store.commit('setSelfBanned', res.body.selfBanned);
    });

    // 获取POST心跳包
    this.heartbeat = setInterval(() => {
      let time = new Date().getTime();
      this._ajax(`${this.$store.state.url}/wx/powderSystem/addPostData`,'GET', {
        uid: userObj.uid,
        openId: userObj.openId,
        groupId: userObj.groupId,
        time
      }, (res) => {
      });
    }, 10000);

    // 判断用户是否属于这个群
    this._ajax(`${this.$store.state.url}/wx/powderSystem/getIsGroupByUser`,'GET', {
      uid: userObj.uid,
      openId: userObj.openId,
      groupId: userObj.groupId
    }, (res) => {
      if (res.body == 0) {
        this.isEwm = true;
      }
    });

    // 查看是否有录播消息
    this.getRecodeData();

    
      // 连接websocket
    // this.MQTTconnect(()=>{
    //   // setTimeout(() => {
    //   //  // 关闭首屏加载
    //   //  this.mask = false;
    //   //  Indicator.close();
    //   // }, 1000);
    // });
    // // 加载历史消息
    // this.loadHistoryData();

    //   // 滚动到底部
    //   setTimeout(() => {
    //     util.partScrollToBottom();
    //   }, 500);


      // 定时消除进来的人数据
      let enterTime = setInterval(() => {
        let list = this.$store.state.practiceObj.enterDataList;
        // if (this.$store.state.practiceObj.enterDataList != false) {
        //   this.$store.state.practiceObj.enterDataList.shift();
        // }
        let time = new Date();
        let m = 5000;
        for (let i in list) {
          if (time.getTime() - list[i].msgTime > m) {
            list.splice(0, 1);
          }
        }
      }, 1000);


      // 验证微信JS-SDK接口
      var accid = 1;
			var currentURL = window.location.href;
      $(document).ready(() => {

        $.post('http://app.yatiku.com/wx/jsapiTicket?urltimer=' + new Date().getTime(), {
          'url': currentURL,
          'accid': accid
        }, (data) => {
