  // 初始化 websocket
  MQTTconnect(cb) {
     mqtt = new Paho.MQTT.Client(
      host,//MQTT域名
      port,//WebSocket端口
      clientId//客户端ClientId
    );
    let message;
    var options = {
      timeout: 3,
      onSuccess: () => {
        // Connection succeeded; subscribe to our topic

         if (localStorage.getItem('enterDataList') != null) {
          let obj = JSON.parse(localStorage.getItem('enterDataList'));
            if (userObj.uid == obj.Uid) {
              let m = 10 * 60 * 1000; // 设置10分钟
              let time = new Date().getTime();
              // 如果超过十分钟了再显示进来了
              if (time.timer - obj.timer > m) {
                  this.MessageSend(JSON.stringify(onlineMessage));
                  localStorage.removeItem('enterDataList');
              }
            } else {
              this.MessageSend(JSON.stringify(onlineMessage));
            }
        } else {
          this.MessageSend(JSON.stringify(onlineMessage));
        }
        cb && cb();
      },
      onFailure: (message) => {
        console.log(JSON.stringify(message));
        setTimeout(this.MQTTconnect, reconnectTimeout);
      }
    };
    mqtt.onConnectionLost = (response) => {
      setTimeout(this.MQTTconnect, reconnectTimeout);
    };
    mqtt.onMessageArrived = (message) => {
      this.MessageArrived(message.destinationName, message.payloadString);
    };
    if (username != null) {
      options.userName = username;
      options.password = password;
    }
    mqtt.connect(options);
    },
