  // 接收websocket
  MessageArrived(top, txt) {
  	console.log(txt);
    try {
      let s = JSON.parse(txt);
      // 消息数据
      if (s.Uid && s.Uid > 0) {
        if (s.Uid == userObj.uid) {
          s.ismy = true;
        } else {
          s.ismy = false;
        }
        if (s.Uid) {
          // dataType：1 为文本消息  2  为练习题   3为签到   33为发起签到   4为用户禁言   44为全体禁言  5为撤回 6为在线人数 7为图片
          let list = this.$store.state.practiceObj.selfSendPracticeList;
          let enterList = this.$store.state.practiceObj.enterDataList;
          if (s.dataType == 1) {
            if (list.length <= 0) {
              let obj = this.setShowTime(s);
              list.push(obj);
              this.getHomeScrollVal(200, () => {
                this.isNewMsg = true;
              });
              // 播放声音
              this.$refs.chatAudio.play();
            } else {
              if (list[list.length - 1].contentId != s.contentId && !s.ismy) {
                let obj = this.setShowTime(s);
                list.push(obj);
                this.getHomeScrollVal(200, () => {
                  this.isNewMsg = true;
                });
                this.autoScroll();
                // 播放声音
                this.$refs.chatAudio.play();
              }
            }

          } else if (s.dataType == 2) {

            if (s.isSubmit) {
              let data = JSON.parse(localStorage.getItem('selectedAnswer'));
              for (let i in data) {
                for (let j in list) {
                  if (data[i].Id == s.Id && list[j].Id == s.Id) {
                    // 动态设置参与人数
                    if (list[j].participate < s.participate) {
                      this.$set(list[j], 'participate', s.participate);
                    }
                  }
                }
              }
            } else {
              if (localStorage.getItem('selectedAnswer')) {
                // 从本地读缓存，看下是否之前答过该题目
                let data = JSON.parse(localStorage.getItem('selectedAnswer'));
                if (s.showSuccessAnswer) {
                  for (let i in data) {
                    // 判断当前接收的试题是否等于，我之前答过的题
                    if (s.Id == data[i].Id) {
                      if (typeof data[i].optionsIndex == 'object') {
                        let answerArr = [];
                        for (let k in s.options) {
                          if (s.options[k].IsTrue == 1) {
                            answerArr.push(k);
                          }
                        }
                        if (data[i].optionsIndex.toString() == answerArr.toString()) {
                          s.isSuccess = true;
                        } else {
                          s.isSuccess = false;
                        }
                        let letter = '';
                        for (let k in data[i].optionsIndex) {
                          letter += this.englishOption[data[i].optionsIndex[k]] + '、';
                        }
                        s.selfAnswer = letter.substring(0, letter.length - 1);
                      } else {
                        if (s.options[data[i].optionsIndex].IsTrue == 1) {
                          s.isSuccess = true;
                        } else {
                          s.isSuccess = false;
                        }
                        s.selfAnswer = data[i].optionsIndex;
                      }

                      s.isSelfAnswer = true;

                      for (let j in list) {
                        if (data[i].Id == list[j].Id) {
                          // 显示自己的答案
                          this.$set(list[j], 'isSuccess', s.isSuccess);
                          this.$set(list[j], 'isSelfAnswer', s.isSelfAnswer);
                          this.$set(list[j], 'selfAnswer', s.selfAnswer);
                        }
                      }
                    }
                  }
                }

                for (let i in data) {
                  for (let j in list) {
                    if (data[i].Id == s.Id && list[j].Id == s.Id) {
                      // 动态设置参与人数
                      if (list[j].participate < s.participate) {
                        this.$set(list[j], 'participate', s.participate);
                      }
                    }
                  }
                }

              }
              if (list.length <= 0) {
                let obj = this.setShowTime(s);
                list.push(obj);
                this.getHomeScrollVal(200, () => {
                  this.isNewMsg = true;
                });
                // 播放声音
                this.$refs.chatAudio.play();
              } else {

                let flag = true;
                for (let i in list) {
                  if (list[i].Timer == s.Timer || list[i].Id == s.Id && s.showSuccessAnswer == undefined) {
                    flag = false;
                  }
                }
                if (flag) {
                  if (!s.ismy) {
                    let obj = this.setShowTime(s);
                    list.push(obj);
                    this.getHomeScrollVal(200, () => {
                      this.isNewMsg = true;
                    });
                    // 播放声音
                    this.$refs.chatAudio.play();
                  }
                }
              }
            }
          } else if (s.dataType == 33) {
            this.signInArr = s;
            localStorage.setItem('signInArr', JSON.stringify(s));
          } else if (s.dataType == 3) {
            if (enterList.length <= 0) {
                enterList.push(s);
            } else {
              if (enterList[enterList.length - 1].contentId != s.contentId && !s.ismy) {
                s.msgTime = new Date().getTime();
                enterList.push(s);
              }
            }
          } else if (s.dataType == 44) {
            this.$store.commit('setGroupBanned', s.banned);

          } else if (s.dataType == 4) {
            // 如果当前禁言的uid等于自己的uid
            let selfBanned = '';
            // 是否是取消当前用户的禁言
            if (s.cancel) {
              if (s.Uid == userObj.uid) {
                selfBanned = false;
              } else {
                selfBanned = true;
              }
            } else {
              if (s.Uid == userObj.uid) {
                selfBanned = true;
              } else {
                selfBanned = false;
              }
            }
            this.$store.commit('setSelfBanned', selfBanned);
		      	for (let i in list) {
              if (list[i].contentId == s.contentId) {
                this.$set(list[i], 'isShowBanned', true);
              }
            }
          } else if (s.dataType == 5) {
            for (let i in list) {
              if (list[i].contentId == s.logId) {
                this.$set(list[i], 'isShowWithdraw', true);
                this.$set(list[i], 'MsgState', 3);
              }
            }
          } else if (s.dataType == 7) {
            if (list.length <= 0) {
              let obj = this.setShowTime(s);
              list.push(obj);
              this.getHomeScrollVal(200, () => {
                this.isNewMsg = true;
              });
              // 播放声音
              this.$refs.chatAudio.play();
            } else {
              if (list[list.length - 1].contentId != s.contentId && !s.ismy) {
                let obj = this.setShowTime(s);
                list.push(obj);
                this.getHomeScrollVal(200, () => {
                  this.isNewMsg = true;
                });
                this.autoScroll();
                // 播放声音
                this.$refs.chatAudio.play();
              }
            }
          } else if (s.dataType == 9) {
            // for (var i = 0; i < 50; i++) {
            //   var sv=JSON.parse('{"Uid":612,"nickname":49,"openid":"gy0ewrlonlipbnut51ntjfw29","dataType":9,"isSign":false,"message":"进来了","timer":1497527696724,"ismy":true,"msgTime":1497527698447}');
            //   sv.msgTime = new Date().getTime();
            //   sv.nickname=i;
            //   this.$store.state.practiceObj.enterDataList.push(sv);
            // }
            // if (localStorage.getItem('enterDataList') != null) {
            //   let obj = JSON.parse(localStorage.getItem('enterDataList'));
            //   if (obj.Uid == s.Uid) {
            //     let m = 10 * 60 * 1000; // 设置10分钟
            //     // 如果超过十分钟了再显示进来了
            //     if (s.timer > - obj.timer > m) {
            //        this.$store.state.practiceObj.enterDataList.push(s);
            //        localStorage.setItem('enterDataList',JSON.stringify(s));
            //     }
            //   } else {
            //     this.$store.state.practiceObj.enterDataList.push(s);
            //   }
            // } else {
            //   this.$store.state.practiceObj.enterDataList.push(s);
            //   localStorage.setItem('enterDataList',JSON.stringify(s));
            // }
            s.msgTime = new Date().getTime();
            this.$store.state.practiceObj.enterDataList.push(s);
            localStorage.setItem('enterDataList',JSON.stringify(s));

          }

        }
      }

      // 在线人数
      if (s.dataType == 6) {
        if (localStorage.getItem('txq_groupName') != null) {
          if (userObj.adminId == 2) {
              let val = `${localStorage.getItem('txq_groupName')}(${s.onlineNumber}/${s.allCount})`;
              if (localStorage.getItem('loginMsg') != null) {
                if (val != localStorage.getItem('loginMsg')) {
                  document.title = val;
                }
              } else {
                 document.title = val;
              }
              localStorage.setItem('loginMsg',val);
          } else {
              let val = `${localStorage.getItem('txq_groupName')}(${s.allCount})`;
              if (localStorage.getItem('loginMsg') != null) {
                if (val != localStorage.getItem('loginMsg')) {
                  document.title = val;
                }
              } else {
                 document.title = val;
              }
              localStorage.setItem('loginMsg',val);
          }
        }
      } 

      // 公告
      else if (s.dataType == 7) {
        this.announcementObj = s;
      }

    } catch (err) {
      // console.log('错误了');
      // console.log(err);
    }
  },
