  // 设置当前时间是否显示
  setShowTime (obj) {
    // 时间显示
    let time = new Date();
    if(localStorage.getItem('timer')) {
      let timer = localStorage.getItem('timer');
      let m = 5 * 60 * 1000;
      if(time.getTime() - timer > m) {
        obj.IsShowTime = true;
        // 如果大于五分钟再保存当前时间的时间戳
        localStorage.setItem('timer', time.getTime());
      } else {
        obj.IsShowTime = false;
      }
    } else {
      // 保存当前时间的时间戳
      localStorage.setItem('timer', time.getTime());
      obj.IsShowTime = true;
    }
    return obj;
  },
  // 获取录播消息
  getRecodeData(timer) {
    let time = null;
    // 优先判断是否是翻页
    if (timer) {
      time = timer;
    } else {
      // 先看上次读到了哪里
      if (localStorage.getItem('recodeTime') != null && localStorage.getItem('recodeId') != null) {
        // 获取最后一次读到的消息的时间戳
        time = localStorage.getItem('recodeTime');
        this.recordedObj.type = 1;
        this.recordedObj.id = localStorage.getItem('recodeId');
      } else {
        	time = new Date().getTime();
      }
    }
	let data;
    if (this.recordedObj.id) {
		data = {
			recodeId: this.recordedObj.id,
			time,
			type: this.recordedObj.type,
			openId: userObj.openId,
			uid: userObj.uid,
      groupId: userObj.groupId
		};
	} else {
		data = {
			time,
			type: this.recordedObj.type,
			openId: userObj.openId,
			uid: userObj.uid,
      groupId: userObj.groupId
		};
	}
    this._ajax(`${this.$store.state.url}/wx/powderSystem/getRecodeMessage`,'GET',data, (res) => {
    

      if (res.body.messageList && res.body.messageList.length && res.body.Id != undefined) {
		    // 设置录播状态
		    this.recordedObj.isRecorded = true;
        let list = this.$store.state.practiceObj.selfSendPracticeList;
        let time = [];
        let message = res.body.messageList;
        let m = 0;
        let nextM = 1;
        let temp = 0;
        let index = 0;
        let timer = setInterval(() => {
            m++;
            
            if (m == nextM) {
              index++;
              if (index+1 == message.length) {
                if (message[index].MsgType == 2) {
                  let jsonData = JSON.parse(message[index].MsgContent);
                  if (jsonData.showSuccessAnswer) {
                      temp = util.random(20,30);
                  } else {
                     temp = util.random(10,15);
                  }
                } else {
                  temp = util.random(3,5);
                }
              } else {
                let _index
                if (index == message.length) {
                  _index = index;
                } else {
                   _index = index + 1;
                }
               
                if (message[_index].MsgType == 2) { 
                  let jsonData = JSON.parse(message[_index].MsgContent);
                  if (jsonData.showSuccessAnswer) {
                      temp = util.random(20,30);
                  } else {
                     temp = util.random(10,15);
                  }
                } else {
                  temp = util.random(3,5);
                }
              }
              nextM = m + temp;
              if (message[index].Uid == userObj.uid) {
                message[index].ismy = true;
              } else {
                message[index].ismy = false;
              }
              
              // 普通文本消息
              if (message[index].MsgType == 1) {
                message[index].dataType = 1;
                message[index].isShowWithdraw = true;
              }

              // 练习题消息
              if (message[index].MsgType == 2) {
                let jsonData = JSON.parse(message[index].MsgContent);
                let res_id = message[index].Id;
                jsonData.contentId = res_id;
                message[index] = jsonData;
                if(message[index].Uid == userObj.uid) {
                  message[index].ismy = true;
                } else {
                  message[index].ismy = false;
                }
              }

              // 签到消息
              if (message[index].MsgType == 3) {
                message[index].dataType = 3;
              }

              // 图片消息
              if (message[index].MsgType == 7) {
                message[index].dataType = 7;
              }

              // 如果是普通消息或者是签到消息
              if (message[index].MsgType == 1 || message[index].MsgType == 3 || message[index].MsgType == 7) {
                message[index].contentId = message[index].Id;
              }

              // 是否是管理员
              message[index].adminId = message[index].IdentityType;

              if (message[index].MsgType != 9) {
                  list.push(message[index]);
              }

              // 每次将当前push的数据缓存起来 
              localStorage.setItem('recodeTime',message[index].Timer);

              // 如果加载完最后一条数据  
              if (index == res.body.messageList.length -1) {
                console.info('加载完毕');
                // 页数+1
                this.recordedObj.type = 1;
                // 进行轮循翻页
                this.getRecodeData(message[index].Timer);
              }
            }
        },1000);

			// 将当前录播id数据缓存起来
			localStorage.setItem('recodeId',res.body.Id);
      this.recordedObj.id = res.body.Id;
      } else {

		// 如果id存在的情况下没有数据, 就清空缓存
		if (localStorage.getItem('recodeId') != null || localStorage.getItem('recodeTime') != null) {
			localStorage.removeItem('recodeId');
			localStorage.removeItem('recodeTime');
		}

    // 设置录播状态
    this.recordedObj.isRecorded = false;
    // 如果没有录播消息，就走正常流程
    console.info('正常流程');
    // 连接websocket
    this.MQTTconnect(()=>{
      // setTimeout(() => {
      //  // 关闭首屏加载
      //  this.mask = false;
      //  Indicator.close();
      // }, 1000);
    });
    // 加载历史消息
    this.loadHistoryData();

    // 滚动到底部
    setTimeout(() => {
      util.partScrollToBottom();
    }, 500);
      }
    });
  },

  // 处理图片发送
  _syncUpload(url) {
    if (url) {
      let time = new Date();
      let obj = {};

      // 时间显示
      if(localStorage.getItem('timer')) {
        let timer = localStorage.getItem('timer');
        let m = 5 * 60 * 1000;
        if(time.getTime() - timer > m) {
          obj.IsShowTime = true;
          // 如果大于五分钟再保存当前时间的时间戳
          localStorage.setItem('timer', time.getTime());
        } else {
          obj.IsShowTime = false;
        }
      } else {
        // 保存当前时间的时间戳
        localStorage.setItem('timer', time.getTime());
        obj.IsShowTime = true;
      }

      obj.MsgType = 7;
      obj.MsgContent = `http://resource.yatiku.com${url}`;
      this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog`,'GET',{
        uid: userObj.uid,
        openId: userObj.openId,
        msgType: obj.MsgType,
        msgContent: obj.MsgContent,
        groupId: userObj.groupId,
        timer: time.getTime(),
        isShowTime: obj.IsShowTime
      }, (res) => {

          obj.Uid = userObj.uid;
          obj.nickname = userObj.nickname;
          obj.headimgurl = userObj.headimgurl;
          obj.contentId = res.body;
          obj.Timer = time.getTime();
          obj.ismy = true;
          obj.dataType = 7;
          obj.OpenId =  userObj.openId;
          obj.adminId = this.show;
          obj.noteText = '图片';
          this.$store.state.practiceObj.selfSendPracticeList.push(obj);

          // 如果还在录播中就不发送websocket
          if (this.recordedObj.isRecorded) {
            if (localStorage.getItem('recodeTime') != null) {
              // 设置发送前的一条消息的时间戳
              obj.Timer = parseInt(localStorage.getItem('recodeTime'), 10) + 2000;
            } 
          } else {
              // 发送websocket
              try {
                this.MessageSend(JSON.stringify(obj));
                console.log(obj);
              }catch(err) {
                Toast('网络连接错误，刷新试试');
              }
              
          }

          // 等待DOM已经被插入进去的时候，再滚动到底部
          this.$nextTick(() => {
            setTimeout(() => {
              util.partScrollToBottom();
            },300);
          });
      });
    }
  },
  // 开始录播
  _openRecorded(val) {
    this._ajax(`${this.$store.state.url}/wx/powderSystem/addRecodeContent`,'GET',{
      type: val,
      recodeId: this.recordedObj.id || undefined,
      groupId: userObj.groupId
    }, (res) => {
      // 开始录播的时候获取录播id
      if (val == 0) {
        this.recordedObj.id = res.body;
      } else {
        //  结束录播的时候清空录播id 
        this.recordedObj.id = '';
      }
    });
  },
  // 选择录播消息
  recorded(event) {
    let messageId = '';
    let target = event.target;
    // 选中
    if (target.localName == 'div') {
      let span = target.children[0];
      if (util.hasClass(span,'on')) {
        util.removeClass(span,'on')
      } else {
        util.addClass(span,'on')
      }
      let spanList = target.parentNode.children;
      for (let i in spanList) {
        if (util.hasClass(spanList[i],'chat-msg')) {
          for (let j in spanList[i].children) {
            if (util.hasClass(spanList[i].children[j],'text')) {
              // 得到当前的消息id
              messageId = spanList[i].children[j].getAttribute('data-contentid');
            }
          }
        }
      }
    } else if (target.localName == 'span') {
      let span = target;
      if (util.hasClass(span,'on')) {
        util.removeClass(span,'on');
      } else {
        util.addClass(span,'on');
      }
      let spanList = target.parentNode.parentNode.children;
      for (let i in spanList) {
        if (util.hasClass(spanList[i],'chat-msg')) {
          for (let j in spanList[i].children) {
            if (util.hasClass(spanList[i].children[j],'text')) {
              // 得到当前的消息id
              messageId = spanList[i].children[j].getAttribute('data-contentid');
            }
          }
        }
      }
    }
    // 添加或删除录播消息
    this._ajax(`${this.$store.state.url}/wx/powderSystem/addRecodeMessage`,'GET',{
      recodeId: this.recordedObj.id,
      messageId
    }, (res) => {
      
    });

  },
