  // 隐藏长按显示的信息
  hideWithDraw() {
    this.$refs.longTap.style.display = 'none';
    this.$refs.longTap.removeAttribute('data-contentid');
    for(let i in this.$refs.longTap.children) {
      let v = parseInt(i, 10);
      if(isNaN(v)) return;
      this.$refs.longTap.children[i].style.display = 'block';
    }
  },
