  // 禁言
  banned(event) {
    let uid = event.target.parentNode.getAttribute('data-uid');
    let openId = event.target.parentNode.getAttribute('data-openid');
    let nickname = event.target.parentNode.getAttribute('data-nickname');
    let headimgurl = event.target.parentNode.getAttribute('data-headimgurl');
