    this._ajax(`${this.$store.state.url}/wx/powderSystem/edUserInfoProhibitState`,'GET',{
      uid,
      openId,
      groupId: userObj.groupId,
      isProhibit: 1
    }, (res) => {
      let obj = {
        Uid: uid,
        openId,
        nickname,
        headimgurl,
		    contentId,
        banned: false,
        dataType: 4,
        noteText: '禁言'
      };
