  // 撤回
  withdraw(event) {
    let uid = event.target.parentNode.getAttribute('data-uid');
    let openId = event.target.parentNode.getAttribute('data-openid');
    let logId = event.target.parentNode.getAttribute('data-contentid');
    let state = this.state;
    let date = new Date();
    let timer = event.target.parentNode.getAttribute('data-time');
    let s = 2 * 60 * 1000;
    if(date.getTime() - timer > s) {
      if (userObj.adminId == 2) {
        this._ajax(`${this.$store.state.url}/wx/powderSystem/edChatLogState`,'GET',{
          logId,
          state,
          adminId: userObj.adminId,
          }, (res) => {
          let obj = {
            Uid: uid,
            logId,
            state,
            withdrawTime: date.getTime(),
            dataType: 5,
            noteText: '撤回'
          };
          this.MessageSend(JSON.stringify(obj));
          this.hideWithDraw();
        });
      } else {
	      Toast("超过两分钟的消息不能被撤回");
      	this.hideWithDraw();
	    }
    } else {

        this._ajax(`${this.$store.state.url}/wx/powderSystem/edChatLogState`,'GET',{
          logId,
          state,
          adminId: userObj.adminId,
        }, (res) => {
          let obj = {
            Uid: uid,
            logId,
            state,
            withdrawTime: date.getTime(),
            dataType: 5,
            noteText: '撤回'
          };
          this.MessageSend(JSON.stringify(obj));
          this.hideWithDraw();
        });
    }
  },
  // 发送题目
  _sendTitle(obj) {
    if(obj) {
      if(obj.parcticeIndex === false) {
        Toast('请选择题目');
        return false;
      } else if(obj.practiceList[obj.parcticeIndex].on) {
        let qid = obj.practiceList[obj.parcticeIndex].QuestionsId;
        this._ajax(`${this.$store.state.url}/wx/powderSystem/getDayPractiseQuestionById`,'GET',{
          groupId: userObj.groupId,
          qid,
          sendType: obj.type,
          adminId: userObj.adminId
        }, (res) => {
          // 发送试题
          // Question.Title     试题标题
          // Question.options   试题选项
          // Question.options.Option  选项内容
          // Question.options.IsTrue  0 错误  1  正确

          // 发送试题和答案
          // Question.Explain  解析
          // Question.People   答题人数
          // Question.Accuracy 全网正确率
          let list = this.$store.state.practiceObj.selfSendPracticeList;
          let time = new Date();
          list.push(res.body.Question);
          let listLength = list.length - 1;
          // 设置每日一练id
          this.$set(list[listLength], 'PractiseItem', obj.practiceList[obj.parcticeIndex].PractiseItem);
          // 设置是否是自己发送的
          this.$set(list[listLength], 'ismy', true);
          // 设置uid
          this.$set(list[listLength], 'Uid', userObj.uid);
          // 设置名称
          this.$set(list[listLength], 'nickname', userObj.nickname);
          // 设置头像
          this.$set(list[listLength], 'headimgurl', userObj.headimgurl);
          // 设置是否是练习题
          this.$set(list[listLength], 'dataType', 2);
          // 设置当前时间戳
          this.$set(list[listLength], 'Timer', time.getTime());
          // 设置当前参与人数
          this.$set(list[listLength], 'participate', res.body.MakeCount);
          // 设置当前管理员id
          this.$set(list[listLength], 'adminId', userObj.adminId);

          if(obj.type == 1) {
            // 隐藏正确答案
            this.$set(list[listLength], 'submitStatus', false);
          } else if(obj.type == 2) {
            // 显示正确答案
            this.$set(list[listLength], 'submitStatus', true);
            this.$set(list[listLength], 'showSuccessAnswer', true);
            this.$set(list[listLength], 'participate', res.data.MakeCount);
          }

          if(localStorage.getItem('timer')) {
            let timer = localStorage.getItem('timer');
            let m = 5 * 60 * 1000;
            if(time.getTime() - timer > m) {
              this.$set(list[listLength], 'IsShowTime', true);
              // 如果大于五分钟再保存当前时间的时间戳
              localStorage.setItem('timer', time.getTime());
            } else {
              this.$set(list[listLength], 'IsShowTime', false);
            }
          } else {
            // 保存当前时间的时间戳
            localStorage.setItem('timer', time.getTime());
            this.$set(list[listLength], 'IsShowTime', true);
          }
          let MsgType = 2;
          let list_content = JSON.stringify(list[listLength]);

          this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog`,'GET', {
            uid: userObj.uid,
            openId: userObj.openId,
            msgType: MsgType,
            msgContent: list_content,
            groupId: userObj.groupId,
            timer: time.getTime(),
            isShowTime: list[listLength].IsShowTime
          }, (res) => {
            // 设置消息id
            this.$set(list[listLength], 'contentId', res.body);
            this.$set(list[listLength], 'noteText', '发送题目')
            this.MessageSend(JSON.stringify(list[listLength]));
          });

          // 异步发送websocket
          // setTimeout(() => {
          //   this.$set(list[listLength], 'noteText', '发送题目')
          //   this.MessageSend(JSON.stringify(list[listLength]));
          // },1000);

          this.$nextTick(() => {
            util.partScrollToBottom();
            // 隐藏更多功能
            this.isMoreFunction = false;
          });
        });
      }
    }
  },
  // 发起签到
  _openSignIn(val) {
    if(val) {
      this._ajax(`${this.$store.state.url}/wx/powderSystem/addUserSign`,'GET', {
        groupId: userObj.groupId,
        adminId: userObj.adminId
      }, (res) => {
        if (res.body > 0) {
            let obj = {
            Uid: userObj.uid,
            dataType: 33,
            show: true,
            id: res.body,
            noteText: '发起签到'
          };
          // 发送websocket
          this.MessageSend(JSON.stringify(obj));
        }
      });
    }
  },
  // 签到
  _emitSignIn(val) {
    this._ajax(`${this.$store.state.url}/wx/powderSystem/addUserSignLog`,'GET', {
      signId: this.signInArr.id,
      uid: userObj.uid,
      openId: userObj.openId,
      groupId: userObj.groupId,
    }, (res) => {
        if (res.body < 0) {
          Toast('已经签到过了');
          this.signInArr.show = false;
          // 删除本地签到的缓存
          localStorage.removeItem('signInArr');
          return false;
        }
        let list = this.$store.state.practiceObj.enterDataList;
        let time = new Date();
        this.signInArr.show = false;
        let obj = {
          Uid: userObj.uid,
          nickname: userObj.nickname,
          headimgurl: userObj.headimgurl,
          msgType: 3,
          dataType: 3,
          show: this.$store.state.practiceObj.signIn,
          MsgContent: res.body,
          ismy: true,
          Timer: time.getTime(),
          adminId: userObj.adminId,
          message: '签到了',
          noteText: '签到'
        };

        if(localStorage.getItem('timer')) {
          let timer = localStorage.getItem('timer');
          let m = 5 * 60 * 1000;
          if(time.getTime() - timer > m) {
            obj.IsShowTime = true;
            // 如果大于五分钟再保存当前时间的时间戳
            localStorage.setItem('timer', time.getTime());
          } else {
            obj.IsShowTime = false;
          }
        } else {
          // 保存当前时间的时间戳
          localStorage.setItem('timer', time.getTime());
          obj.IsShowTime = true;
        }
        this.$refs.loadmore.$el.style.paddingBottom = '60px';

        this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog`,'GET', {
          uid: userObj.uid,
          openId: userObj.openId,
          msgType: obj.msgType,
          msgContent: obj.MsgContent,
          groupId: userObj.groupId,
          timer: time.getTime(),
          isShowTime: obj.IsShowTime
        }, (res) => {
          obj.contentId = res.body;
          list.push(obj);
          let listLength = list.length - 1;
          // 发送websocket
          this.MessageSend(JSON.stringify(obj));
          // 删除本地签到的缓存
          localStorage.removeItem('signInArr');
        });

        // 滚动到底部
        this.$nextTick(() => {
          util.partScrollToBottom();
        });
    });
  },
  // 消息touchstart事件
  msgTouchstart(event) {
    this.timeOutEvent = setTimeout(() => {
      this.timeOutEvent = 0;
      let contentId,uid,openId,nickname,headimgurl,time,content,longTap;
      if (event.target.localName == 'img') {
        contentId = event.target.parentNode.getAttribute('data-contentid');
        uid = event.target.parentNode.getAttribute('data-uid');
        openId = event.target.parentNode.getAttribute('data-openid');
        nickname = event.target.parentNode.getAttribute('data-nickname');
        headimgurl = event.target.parentNode.getAttribute('data-headimgurl');
        time = event.target.parentNode.getAttribute('data-time');
        content = event.target.parentNode.getAttribute('data-content');
        longTap = this.$refs.longTap;
      } else {
        contentId = event.target.getAttribute('data-contentid');
        uid = event.target.getAttribute('data-uid');
        openId = event.target.getAttribute('data-openid');
        nickname = event.target.getAttribute('data-nickname');
        headimgurl = event.target.getAttribute('data-headimgurl');
        time = event.target.getAttribute('data-time');
        content = event.target.getAttribute('data-content');
        longTap = this.$refs.longTap;
      }
      
      // 要先显示，否则后面获取不到宽度
      longTap.style.display = '-webkit-box';
      longTap.style.display = 'flex';
      let longTap_width = parseInt(longTap.clientWidth, 10);
      let longTap_height = parseInt(longTap.clientHeight, 10);
      let document_width = parseInt(document.body.clientWidth, 10);
      let document_height = parseInt(document.body.clientHeight, 10);
      let y = event.changedTouches[0].clientY - (longTap_height + 20);
      let x = event.changedTouches[0].clientX - (longTap_width / 2);


      // 如果长按的信息不是自己发送的，或者如果不是管理员的情况下，就隐藏禁言和撤回
      if(uid != userObj.uid || this.$store.state.adminId != 2) {
        this.$refs.banned.style.display = 'none';
        this.$refs.withdraw.style.display = 'none';
      }
      // 如果长按的信息不是自己发送的，并且如果是管理员的情况下，就显示禁言
      if(uid != userObj.uid && this.$store.state.adminId == 2) {
        this.$refs.banned.style.display = 'block';
        this.$refs.withdraw.style.display = 'block';
      }
	    // 如果长按的信息是自己发送的，就隐藏禁言
      if(uid == userObj.uid) {
        this.$refs.banned.style.display = 'none';
		    this.$refs.withdraw.style.display = 'block';
      }
      console.log(event);
      longTap.setAttribute('data-uid', uid);
      longTap.setAttribute('data-contentid', contentId);
      longTap.setAttribute('data-openid', openId);
      longTap.setAttribute('data-nickname', nickname);
      longTap.setAttribute('data-headimgurl', headimgurl);
      longTap.setAttribute('data-time', time);
      $('.copys').attr('data-clipboard-text',content);

      var clipboard = new Clipboard('.copys');

      // 防止出界
      if(x < 0) {
        x = 0;
      } else if(x >= document_width - longTap_width) {
        x = document_width - longTap_width - 2;
      }
      if(y < 0) {
        y = 0;
      } else if(y >= document_height - longTap_height) {
        y = document_height - longTap_height - 2;
      }

      longTap.style.left = x + 'px';
      longTap.style.top = y + 'px';
    }, 500);
  },
  // 消息touchmove事件
  msgTouchmove(event) {
    clearTimeout(this.timeOutEvent);
    this.timeOutEvent = 0;
  },
  // 消息touchend事件
  msgTouchend(event) {
    clearTimeout(this.timeOutEvent);
    this.timeOutEvent = 0;
  },
  // 输入框点击
  getInputClick(event) {
    let input = event.target;
    let doc = document.body || document.documentElement;
	  this.isMoreFunction = false;
    setTimeout(() => {
      //  解决ios键盘弹起被挡住的问题
      doc.scrollTop = doc.clientHeight;
      input.scrollIntoViewIfNeeded();
      // 隐藏长按显示的信息
      this.hideWithDraw();
    }, 300)
  },
