  getInputFocus() {
    // 隐藏更多功能和表情
    this.isMoreFunction = false;
    this.isExpression = false;
  },
  // 练习题选项单选
  practiceOptionsClick(event, index, type) {
    // 当前元素所有的同级元素删除 'on' 样式
    let li = '';
    let btn = '';
    let target = event.target;
    if(target.localName == 'b' || target.localName == 'span') {
      li = target.parentNode.parentNode.children;
      btn = target.parentNode.parentNode.parentNode.parentNode.children;
    } else if(target.localName == 'li') {
      li = target.parentNode.children;
      btn = target.parentNode.parentNode.parentNode.children;
    }

    switch(type) {
      // type 2 单选题
      case '2':
        for(let i in li) {
          util.removeClass(li[i], 'on');
        }
        // 当前选中的元素添加 'on' 样式
        util.addClass(li[index], 'on');
        break;
        // type 3 多选题
      case '3':
        if(util.hasClass(li[index], 'on')) {
          util.removeClass(li[index], 'on');
        } else {
          util.addClass(li[index], 'on');
        }
        break;
    }

    // 将提交按钮添加 'active' 样式
    let active = false;
    for(let i in li) {
      if(util.hasClass(li[i], 'on')) {
        active = true;
      }
    }
    if(active) {
      for(let i in btn) {
        if(util.hasClass(btn[i], 'text-btn')) {
          util.addClass(btn[i], 'active');
        }
      }
    } else {
      for(let i in btn) {
        if(util.hasClass(btn[i], 'text-btn')) {
          util.removeClass(btn[i], 'active');
        }
      }
    }

  },
  // 练习题选项提交
  practiceOptionsSubmit(event, index, type) {
    let list = this.$store.state.practiceObj.selfSendPracticeList[index];
    let textRadio = event.target.parentNode.children;
    let li = '';
    let optionsIndex = false;
    let optionsId = [];
    for(let i in textRadio) {
      if(util.hasClass(textRadio[i], 'text-radio')) {
        li = textRadio[i].children[0].children;
      }
    }
    switch(type) {
      // 单选题
      case '2':
        for(let i in li) {
          if(util.hasClass(li[i], 'on')) {
            optionsIndex = i;
          }
        }
        break;
        // 多选题
      case '3':
        optionsIndex = [];
        for(let i in li) {
          if(util.hasClass(li[i], 'on')) {
            // 保存多个答案
            optionsIndex.push(i);
            optionsId.push(li[i].getAttribute('data-id'));
          }
        }
        break;
    }
    if(!optionsIndex) {
      Toast('请选择你的答案');
      return false;
    }

    // 提交按钮状态
    this.$set(list, 'submitStatus', true);

    switch(type) {
      // 单选题
      case '2':
        // 判断正确答案
        if(list.options[optionsIndex].IsTrue == 1) {
          // 保存用户答题记录
          this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog`,'GET', {
            qid: list.Id,
            optionId: list.options[optionsIndex].Id,
            isTrue: 0,
            uid: userObj.uid,
            openId: userObj.openId,
            addDayPractiseQuestionLog: list.PractiseItem,
            groupId: userObj.groupId
          }, (res) => {
            let obj = {
              Uid: list.Uid,
              Id: list.Id,
              participate: res.body,
              dataType: 2,
              isSubmit: true,
              noteText: '练习题选项提交'
            };
            // 保存参与人数
            this.$set(list, 'participate', res.body);
            this.$set(list, 'isSubmit',true);
            this.$set(list, 'noteText','练习题选项提交');
            this.MessageSend(JSON.stringify(obj));
          });
        } else {
          // 保存用户答题记录
          this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog`,'GET', {
            qid: list.Id,
            optionId: list.options[optionsIndex].Id,
            isTrue: 1,
            uid: userObj.uid,
            openId: userObj.openId,
            addDayPractiseQuestionLog: list.PractiseItem,
            groupId: userObj.groupId
          }, (res) => {
             let obj = {
              Uid: list.Uid,
              Id: list.Id,
              participate: res.body,
              dataType: 2,
              isSubmit: true,
              noteText: '练习题选项提交'
            };
            // 保存参与人数
            this.$set(list, 'participate', res.body);
            this.$set(list, 'isSubmit',true);
            this.$set(list, 'noteText','练习题选项提交');
            this.MessageSend(JSON.stringify(obj));
          });
        }
        break;
        // 多选题
      case '3':
        let thisOptions = [];
        for(let i in list.options) {
          if (list.options[i].IsTrue == 1) {
            thisOptions.push(i);
          }
        }
        if(optionsIndex.toString() == thisOptions.toString()) {
          // 保存用户答题记录
          this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog`,'GET', {
            qid: list.Id,
            optionId: optionsId.toString(),
            isTrue: 0,
            uid: userObj.uid,
            openId: userObj.openId,
            addDayPractiseQuestionLog: list.PractiseItem,
            groupId: userObj.groupId
          }, (res) => {
             let obj = {
              Uid: list.Uid,
              Id: list.Id,
              participate: res.body,
              dataType: 2,
              isSubmit: true,
              noteText: '练习题选项提交'
            };
            // 保存参与人数
            this.$set(list, 'participate', res.body);
            this.$set(list, 'isSubmit',true);
            this.$set(list, 'noteText','练习题选项提交');
            this.MessageSend(JSON.stringify(obj));
          });
        } else {
          // 保存用户答题记录
          this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog`,'GET', {
            qid: list.Id,
            optionId: optionsId.toString(),
            isTrue: 1,
            uid: userObj.uid,
            openId: userObj.openId,
            addDayPractiseQuestionLog: list.PractiseItem,
            groupId: userObj.groupId
          }, (res) => {
             let obj = {
              Uid: list.Uid,
              Id: list.Id,
              participate: res.body,
              dataType: 2,
              isSubmit: true,
              noteText: '练习题选项提交'
            };
            // 保存参与人数
            this.$set(list, 'participate', res.body);
            this.$set(list, 'isSubmit',true);
            this.$set(list, 'noteText','练习题选项提交');
            this.MessageSend(JSON.stringify(obj));
          });
        }
        break;
    }

    let answer = [];
    if(localStorage.getItem('selectedAnswer')) {
      answer = JSON.parse(localStorage.getItem('selectedAnswer'));
      for(let i in answer) {
        // 判断是否做过相同的题
        if(answer[i].Id == list.Id) {
          answer.splice(i, 1);
        }
      }
    }
    answer.push({
      Uid: userObj.uid,
      Id: list.Id,
      optionsIndex
    });
    // 保存当前选中的题和答案到本地缓存
    localStorage.setItem('selectedAnswer', JSON.stringify(answer));
  },
