    });
  },
  // 按钮发送
  chatInputSend() {
    if(this.chatInput) {
      this._send();
    }
  },
  // 加载历史消息
  loadHistoryData(id, cb) {
	let data = {};
	if (id == undefined || id == '') {
		data.groupId = userObj.groupId;
	} else {
		data.id = id;
		data.groupId = userObj.groupId;
	}
    // 默认加载历史消息
    this._ajax(`${this.$store.state.url}/wx/powderSystem/getUserMessage`,'GET',data, (res) => {

      let list = this.$store.state.practiceObj.selfSendPracticeList;
      for (var i in res.body) {

	

        if (res.body[i].Uid == userObj.uid) {
          res.body[i].ismy = true;
        } else {
          res.body[i].ismy = false;
        }

        // 普通文本消息
        if (res.body[i].MsgType == 1) {
          res.body[i].dataType = 1;
          res.body[i].isShowWithdraw = true;
        }

        // 练习题消息
        if (res.body[i].MsgType == 2) {
          let jsonData = JSON.parse(res.body[i].MsgContent);
          // 从本地读缓存是否之前有做过该题目
          if(localStorage.getItem('selectedAnswer') != null) {
            let answer = JSON.parse(localStorage.getItem('selectedAnswer'));

            // 是否是解析过的题目
            if (jsonData.showSuccessAnswer) {
              for (let i in answer) {
                  // 判断当前接收的试题是否等于，我之前答过的题
                  if (jsonData.Id == answer[i].Id) {

                    // 判断是否是多选题
                    if (typeof answer[i].optionsIndex == 'object') {
                      let answerArr = [];
                      for (let k in jsonData.options) {
                        if (jsonData.options[k].IsTrue == 1) {
                          answerArr.push(k);
                        }
                      }
                      if (answer[i].optionsIndex.toString() == answerArr.toString()) {
                        jsonData.isSuccess = true;
                      } else {
                        jsonData.isSuccess = false;
                      }
                      let letter = '';
                      for (let k in answer[i].optionsIndex) {
                        letter += this.englishOption[answer[i].optionsIndex[k]] + '、';
                      }
                      jsonData.selfAnswer = letter.substring(0, letter.length - 1);
                    } else {
                      if (jsonData.options[answer[i].optionsIndex].IsTrue == 1) {
                        jsonData.isSuccess = true;
                      } else {
                        jsonData.isSuccess = false;
                      }
                      jsonData.selfAnswer = answer[i].optionsIndex;
                    }

                    jsonData.isSelfAnswer = true;

                    for (let j in list) {
                      if (answer[i].Id == list[j].Id) {
                        // 显示自己的答案
                        this.$set(list[j], 'isSuccess', jsonData.isSuccess);
                        this.$set(list[j], 'isSelfAnswer', jsonData.isSelfAnswer);
                        this.$set(list[j], 'selfAnswer', jsonData.selfAnswer);
                      }
                    }
                  }
              }
              for (let i in answer) {
                for (let j in list) {
                  if (answer[i].Id == jsonData.Id && list[j].Id == jsonData.Id) {
                    // 动态设置参与人数
                    this.$set(list[j], 'participate', jsonData.participate);
                  }
                }
              }
            } else {
              for(let j in answer) {
                if(answer[j].Id == jsonData.Id && answer[j].Uid == userObj.uid) {
                  // 设置提交按钮不可见
                  jsonData.submitStatus = true;
                  // 显示已选中的值
                  jsonData.checkedOption = answer[j].optionsIndex;
                  // 多选 
                  if(jsonData.Style == '3') {
                    for(let k in answer[j].optionsIndex) {
                      jsonData.options[answer[j].optionsIndex[k]].isChecked = true;
                    }
                  }
                  this._ajax(`${this.$store.state.url}/wx/powderSystem/getDayPractiseQuestionByIdCount`, 'GET', {
                    id: answer[j].Id
                  }, (res) => {
                    jsonData.participate = res.body;
                  });
                }
             }
            }
            
          }
          let res_id = res.body[i].Id;
          jsonData.contentId = res_id;
          res.body[i] = jsonData;
          if(res.body[i].Uid == userObj.uid) {
            res.body[i].ismy = true;
          } else {
            res.body[i].ismy = false;
          }
        }

        // 签到消息
        if (res.body[i].MsgType == 3) {
          res.body[i].dataType = 3;
        }

        // 图片消息
        if (res.body[i].MsgType == 7) {
          res.body[i].dataType = 7;
        }

        // 如果是普通消息或者是签到消息
        if (res.body[i].MsgType == 1 || res.body[i].MsgType == 3 || res.body[i].MsgType == 7) {
          res.body[i].contentId = res.body[i].Id;
        }

        // 是否是管理员
        res.body[i].adminId = res.body[i].IdentityType;

        if (id && id != undefined) {
          list.unshift(res.body[i]);
          if(res.body[i].contentId) {
            // 保存第一条历史消息id
            this.historyMsgId = res.body[res.body.length - 1].contentId;
          } else {
            // 保存第一条历史消息id
            this.historyMsgId = res.body[res.body.length - 1].Id;
          }
        } else {
          if(res.body[i].contentId) {
            // 保存第一条历史消息id
            this.historyMsgId = res.body[0].contentId;
          } else {
            // 保存第一条历史消息id
            this.historyMsgId = res.body[0].Id;
          }
          if (res.body[i].MsgType != 9) {
              list.push(res.body[i]);
          }
		  
        }

      }
      cb && cb();
    });
  },
  // ajax 请求
  _ajax(url, type, data, cb) {
    this.$http({
      url: url,
      method: type,
      params: data
    })
    .then(res => {
      if(res.status === 200) {
        cb && cb(res);
      }
    }, error => {
      console.log(error);
    });
  },
  // 发送
  _send() {
    // 消息类型 1为普通文本  2为练习题   3为签到
    let MsgType = 1;
    // 获取消息id
    let contentId = null;
    let time = new Date();
    let obj = {};
    // 解决莫名其妙输入为空的情况下，赋值一个大空格
    let contents = this.chatInput;
    if (!contents || contents == '' || contents.length == 0) {
      Toast('不能输入空噢');
      return false;
    }

    // 时间显示
    if(localStorage.getItem('timer')) {
      let timer = localStorage.getItem('timer');
      let m = 5 * 60 * 1000;
      if(time.getTime() - timer > m) {
        obj.IsShowTime = true;
        // 如果大于五分钟再保存当前时间的时间戳
        localStorage.setItem('timer', time.getTime());
      } else {
        obj.IsShowTime = false;
      }
    } else {
      // 保存当前时间的时间戳
      localStorage.setItem('timer', time.getTime());
      obj.IsShowTime = true;
    }

    this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog`,'GET',{
      uid: userObj.uid,
      openId: userObj.openId,
      msgType: MsgType,
      msgContent: contents,
      groupId: userObj.groupId,
      timer: time.getTime(),
      isShowTime: obj.IsShowTime
    }, (res) => {

      obj.Uid = userObj.uid;
      obj.nickname = userObj.nickname;
      obj.headimgurl = userObj.headimgurl;
      obj.MsgType = MsgType;
      obj.MsgContent = contents;
      obj.contentId = res.body;
      obj.Timer = time.getTime();
      obj.ismy = true;
      obj.dataType = 1;
      obj.OpenId =  userObj.openId;
      obj.adminId = userObj.adminId;
      obj.noteText = '普通文本消息';
      this.$store.state.practiceObj.selfSendPracticeList.push(obj);

      // 如果还在录播中就不发送websocket
      if (this.recordedObj.isRecorded) {
        if (localStorage.getItem('recodeTime') != null) {
          // 设置发送前的一条消息的时间戳
          obj.Timer = parseInt(localStorage.getItem('recodeTime'), 10) + 2000;
        } 
      } else {
          // 发送websocket
          try {
            this.MessageSend(JSON.stringify(obj));
          }catch(err) {
            this.chatInput = '';
            Toast('网络连接错误，刷新试试');
          }
          
      }

      // 等待DOM已经被插入进去的时候，再滚动到底部
      this.$nextTick(() => {
        util.partScrollToBottom();
      });
      
      this.chatInput = '';
    });

    



  }
  },
  // 注册组件
  components: {
    mtLoadmore: Loadmore,
    ewm,
    msgTime,
    msgWithdraw,
	  msgBanned,
    moreFunction,
    enterExaminationResult,
    signIn,
    expression,
    'v-mask': mask
  }
}
</script>

