      background: rgba(0,0,0,.7);
      color: #54DAF9;
      font-size: 12px;
      text-align: center;
      .on{
        color: #fff;
      }
    }
  }

  @keyframes checkbox_left {
    100% {
      width: 7px;
    }
  }
  @keyframes checkbox_right {
    100% {
      width: 11px;
    }
  }
</style>


