<template>
	<div class="practice-main" v-if="myIsPractice">
		<div class="practice-dialog">
				<div class="practice-box">
					<div class="practice-number">
            <div class="practice-head">
              <b @click="tabToggle($event,index)" :class="{'on': index == 0}" v-for="(item,index) in practiceList">{{item.Name}}</b>
            </div>
						<div class="practice-item" v-for="(item,index) in list">
							<span :class="{'on': item.on, 'success': item.SendQuestion > 0, 'error': item.SendAnswer > 0}" @click="practiceTitleSelect(index)">{{item.Id}}</span>
						</div>
					</div>
					<div class="practice-btn">
						<a href="javascript:;" @click="sendTitle('1')">题目</a>
						<a href="javascript:;" @click="sendTitle('2')">解析</a>
					</div>
				</div>
				<div class="practice-close">
					<i @click="closePracticeTitle" class="iconfont icon-20150825tubiaolianxizhuanhuan04"></i>
				</div>
			</div>
			<!-- 遮罩 -->
			<v-mask :flag="myIsPractice"></v-mask>
	</div>
</template>

<script>

// 工具函数
import util from '@/assets/js/util'

// 遮罩组件
import mask from '../functionComponents/mask'

// 消息提示框组件
import { Toast } from 'mint-ui';

export default {
  name: 'practicemain',
	props: ['isPractice'],
  data () {
    return {
			// 1. 解决子组件无法修改父组件的值，创建一个副本
			myIsPractice: this.isPractice,
			// 练习题列表 
      list: [],
			practiceList: [],
      // 存储当前栏目的索引
      columnIndex: 0,
			// 保存当前选择的练习题索引
			parcticeIndex: false,
      // 当前默认选中的
      tabIndex: '1'
    }
  },
	watch: {
		isPractice(val) {
			// 2. 新增isPractice的watch，监听变更并同步到myIsPractice上
			this.myIsPractice = val;
			if (this.myIsPractice) {
				console.log('进来了');
				this._ajax(`${this.$store.state.url}/wx/powderSystem/getDayPractiseQuestionList`, (res) => {
          console.log(res);
					// SendAnswer 已发送：绿色
					// SendQuestion  发送解析：红色
					this.practiceList = res.body;
          this.list = this.practiceList[this.columnIndex].questionlist;
					for (let i in this.practiceList) {
            for (let j in this.practiceList[i].questionlist) {
              		this.$set(this.practiceList[i].questionlist[j],'on',false);
            }
					}
				})


			}
		},
		myIsPractice(val) {
			 this.$emit("on-parctice-change",val);// 3.组件内对myIsPractice变更后向外部发送事件通知
		}
	},
  methods: {
    // tab切换
    tabToggle(event,index) {
      let b = event.target.parentNode.children;
      for (let i in b) {
        util.removeClass(b[i],'on');
      }
      util.addClass(event.target, 'on');
      this.list = this.practiceList[index].questionlist;
      this.columnIndex = index;
      for (let i in this.list) {
        this.list[i].on = false;
      }
    },
  	// 练习题题目选择
    practiceTitleSelect(index) {
			this.parcticeIndex = index;
      for (let i in this.practiceList[this.columnIndex].questionlist) {
        if (i == index) {
          console.log('true');
						this.practiceList[this.columnIndex].questionlist[i].on = true;
        } else {
          console.log('false');
 						this.practiceList[this.columnIndex].questionlist[i].on = false;
        }
      }
    },
  	// 关闭练习题题目
    closePracticeTitle() {
      this.myIsPractice = !this.myIsPractice;
			this.parcticeIndex = false;
    },
		// 发送题目
		// type：1  为  发送题目
		// type：2  为  发送题目和答案
		sendTitle(type) {
			let obj = {
				parcticeIndex: this.parcticeIndex,
				practiceList: this.practiceList[this.columnIndex].questionlist,
				type
			};
			this.$emit('sendTitle',obj);
			this.closePracticeTitle();
		},
		// ajax 请求
    _ajax(url, cb) {
      this.$http.get(url).then( res => {
        if (res.status === 200){
            cb && cb(res);
        }
      }, error => {
       console.log(error);
     });
		}
  },
	components: {
		'v-mask': mask
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
/* 练习题框 */
.practice-dialog{
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 99;
  width: 90%;
  transform: translate(-50%, -50%);
  .practice-box{
    padding: 15px;
    border-radius: 8px;
    background: #fff;
    .practice-number{
      display: flex;
      flex-wrap: wrap;
      .practice-head{
        b{
          padding-bottom: 7px;
          margin-right: 10px;
          font-size: $font16;
          &.on{
            border-bottom: 3px solid $bluecolor;
          }
        }
      }
      .practice-item{
        display: block;
        margin-top: 30px;
        margin-bottom: 10px;
        flex: 0 0 20%;
        min-width: 0;
        text-align: center;
        span{
          display: inline-block;
          width: 40px;
          height: 40px;
          line-height: 40px;
          border: 1px solid $bordercolor;
          border-radius: 50%;
          color: $txtcolor;
          &.success{
            background: #83ce83;
            color: #fff;
          }
          &.error{
            background: $errorcolor;
            color: #fff;
          }
					&.on{
            background: #969696;
            color: #fff;
          }
        }
      }
    }
    .practice-btn{
      margin-top: 30px;
      display: flex;
      a{
        display: block;
        padding: 10px;
        flex: 1;
        background: $bluecolor;
        box-sizing: border-box;
        border-radius: 20px;
        text-align: center;
        color: #fff;
        &:nth-child(1) {
          margin-right: 15px;
        }
      }
    }
  }
  .practice-close{
    margin-top: 20px;
    text-align: center;
    .iconfont{
      font-size: 40px;
      color: #fff;
    }
  }
}
</style>



